import { OrderedMap } from "immutable";
import { Dispatch } from "redux";
import { createStandardAction } from "typesafe-actions";

import RenExSDK, { OrderStatus } from "@renex/renex";

import { setAlert } from "../../actions/alert/alertActions";
import { addPendingAlert } from "../../actions/alert/pendingAlertActions";
import { _captureBackgroundException_ } from "../../lib/errors";
import { Alert, AlertType } from "../../reducers/types";
import { MatchDetailsRecord, TraderOrderRecord } from "../../reducers/typesSdk";

export const syncTraderOrders = createStandardAction("SYNC_TRADER_ORDERS")<{ traderOrders: OrderedMap<string, TraderOrderRecord> }>();

export const addOrders = createStandardAction("ADD_ORDERS")<TraderOrderRecord[]>();

export const updateOrderStatus = createStandardAction("UPDATE_ORDER")<{ orderID: string; orderStatus: OrderStatus }>();

export const updateOrderCancelling = createStandardAction("UPDATE_ORDER_CANCELLING")<{ orderID: string; orderCancelling: boolean }>();

export const cancelLocalOrder = createStandardAction("CANCEL_LOCAL_ORDER")<{ orderID: string }>();

/**
 * Filter for orders that don't need to be checked - based on network and status
 */
export const defaultOrderFilter = (checkAllOrders: boolean) => ((order: TraderOrderRecord | undefined) => {
    if (order === undefined) {
        return false;
    }

    if (!checkAllOrders) {
        if (order.status !== OrderStatus.OPEN &&
            order.status !== OrderStatus.NOT_SUBMITTED &&
            order.status !== OrderStatus.CONFIRMED
        ) {
            return false;
        }
    }

    return true;
});

export const checkOrderStatuses = (
    sdk: RenExSDK,
    traderOrders: OrderedMap<string, TraderOrderRecord>,
    checkAllOrders = false,
) => async (dispatch: Dispatch) => {

    // Filter if order is for different network or trader
    const toCheck = traderOrders
        .valueSeq()
        .filter(defaultOrderFilter(checkAllOrders))
        .toArray();

    for (let order of toCheck) {
        if (order === undefined) { return; }

        // Check the status of the order in the orderbook.
        let orderStatus;
        try {
            orderStatus = await sdk.fetchOrderStatus(order.id);
        } catch (error) {
            _captureBackgroundException_(
                error as Error,
                { description: "Error in checkOrderStatuses action" },
            );
            continue;
        }

        // If the order is settled and doesn't have match details, get them now
        if (!order.matchDetails && orderStatus === OrderStatus.SETTLED) {
            order = order.set("matchDetails", new MatchDetailsRecord(await sdk.fetchMatchDetails(order.id)));
            order = order.set("status", OrderStatus.SETTLED);

            // Only update order after the balances have been updated
            const alert = async () => {
                dispatch(addOrders([order]));

                dispatch(setAlert({
                    alert: new Alert({
                        alertType: AlertType.Success,
                        // TODO: Replace with execution details
                        message: "Your order has successfully been settled",
                    })
                }));
            };
            dispatch(addPendingAlert({ id: `settled: ${order.id}`, method: alert }));

        } else if (order.status !== orderStatus) {
            dispatch(updateOrderStatus({ orderID: order.id, orderStatus }));
        }
    }
};
