import moment from "moment";
import * as React from "react";

import { connect } from "react-redux"; // Custom typings

import { BalanceAction, TransactionStatus } from "@renex/renex";

import { Heading } from "./Heading";
import { Loading } from "./Loading";

import { getTimeMagnitude, naturalTime, TimeMagnitude } from "../lib/conversion";
import { TokenDetails, UnknownToken } from "../lib/market";
import { networkData } from "../lib/network";
import { ApplicationData } from "../reducers/types";

/**
 * BalanceHistory is a visual component for displaying previous deposits and
 * withdrawals
 */
class BalanceHistoryClass extends React.Component<Props, State> {

    /**
     * highlightTimeout is used to time the highlighting of the most recent
     * balance history item.
     */
    public highlightTimeout: NodeJS.Timer | undefined;

    /**
     * datesRerenderTimeout is used to update the balance history item dates
     */
    public datesRerenderTimeout: NodeJS.Timer | undefined;

    public constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            highlightLatest: false,
            receivedBalances: false,
        };
    }

    public componentWillReceiveProps(nextProps: Props): void {
        // Highlight the latest item if it was just added.
        if (this.props.balanceHistory.size < nextProps.balanceHistory.size) {
            if (this.state.receivedBalances) {
                this.setState({ highlightLatest: true });

                if (this.highlightTimeout) { clearTimeout(this.highlightTimeout); }
                this.highlightTimeout = setTimeout(
                    () => {
                        this.setState({ highlightLatest: false });
                    },
                    3 * 1000
                );
            } else {
                this.setState({ receivedBalances: true });
            }
        }
    }

    public componentWillUnmount(): void {
        if (this.highlightTimeout) { clearTimeout(this.highlightTimeout); }
        if (this.datesRerenderTimeout) { clearTimeout(this.datesRerenderTimeout); }
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { balanceHistory } = this.props;
        let relevantRows = null;
        if (balanceHistory.size > 0) {
            // Find the most precise time being shown (seconds, minutes, hours...)
            // and rerender to ensure that these times are updated
            const magnitude = balanceHistory
                .valueSeq()
                .toArray()
                .map((value: BalanceAction) => getTimeMagnitude(value.time))
                .reduce((val: number, min: number) => Math.min(val, min), TimeMagnitude.Day);

            if (this.datesRerenderTimeout) { clearTimeout(this.datesRerenderTimeout); }
            this.datesRerenderTimeout = setTimeout(
                () => {
                    this.forceUpdate();
                },
                magnitude / 2
            );

            relevantRows = this.generateRows();
        }
        return (
            <div className="section history">
                <div className="container">
                    <Heading title="Recent legacy deposits and withdrawals" />
                    <div className="table--wrapper">
                        <table>
                            <thead>
                                <tr>
                                    {/* If any fields are added/removed, the colSpan
                                below must be modified to reflect this. */}
                                    <th>Transaction Hash</th>
                                    <th>Date</th>
                                    <th>Action</th>
                                    <th>Amount</th>
                                </tr>
                            </thead>
                            <tbody>
                                {relevantRows !== null ?
                                    relevantRows // Output relevant rows
                                    :
                                    <tr>
                                        <td colSpan={4}>No transactions</td>
                                    </tr>
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }

    /**
     * generateRows is used to assemble the rows in the table
     */
    private generateRows(): Array<React.ReactNode | null> | null {
        const { balanceHistory } = this.props;
        const { highlightLatest } = this.state;
        let relevantRows = false;

        const rows = balanceHistory
            .valueSeq()
            .toArray()
            .reverse()
            .map((balanceAction: BalanceAction, key: number) => {
                relevantRows = true;
                const TX_URL = `${networkData.etherscan}/tx/${balanceAction.txHash}`;

                const readableAmount = balanceAction.amount;

                const currentMoment = moment(balanceAction.time * 1000)
                    .format("MMMM Do YYYY, HH:mm:ss");

                const actionType = balanceAction.action
                    .charAt(0)
                    .toUpperCase()
                    +
                    balanceAction.action.slice(1);

                return (
                    <tr key={key} className={`${key === 0 && highlightLatest ? "recent" : ""} balanceAction--${balanceAction.status}`}>
                        <td className="monospace">
                            {balanceAction.status === TransactionStatus.Pending ?
                                < Loading /> :
                                ""
                            }
                            <a href={TX_URL} rel="noopener noreferrer" target="_blank">{balanceAction.txHash}</a>
                            {balanceAction.status === TransactionStatus.Failed ? <span className="red"> (failed)</span> : ""}
                            {balanceAction.status === TransactionStatus.Replaced ? <span className="red"> (replaced)</span> : ""}
                        </td>
                        <td title={currentMoment}>
                            <span>{naturalTime(balanceAction.time, { message: "Just now", suffix: "ago", countDown: false })}</span>
                        </td>
                        <td>{actionType}</td>
                        <td>{readableAmount.toFixed()} {TokenDetails.get(balanceAction.token, UnknownToken).symbol}</td>
                    </tr>
                );
            });
        return relevantRows ? rows : null;
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    balanceHistory: state.balanceHistory.balanceHistory,
    sdk: state.sdk.sdk,
    updateIndex: state.balanceHistory.updateIndex,
});

interface Props extends ReturnType<typeof mapStateToProps> {
}

interface State {
    /**
     * highlightLatest indicates if a new balance history item is being
     * highlighted.
     */
    highlightLatest: boolean;

    /**
     * receivedBalances is used to prevent highlighting old items when they
     * are initially received from the SDK storage
     */
    receivedBalances: boolean;
}

export const BalanceHistory = connect(mapStateToProps)(BalanceHistoryClass);
