import * as React from "react";

import { connect } from "react-redux"; // Custom typings

import { AtomicConnectionStatus, OrderSettlement } from "@renex/renex";

import { Pair, Pairs } from "../lib/market";
import { ApplicationData, UnknownMarketPrice } from "../reducers/types";
import { AtomicNotice } from "./AtomicNotice";
import { Heading } from "./Heading";
import { NewOrderInputs } from "./NewOrderInputs";
import { SelectMarket } from "./SelectMarket";
import { WrapBitcoin } from "./WrapBitcoin";

/**
 * NewOrder is a visual component for allowing users to open new orders
 */
class NewOrderClass extends React.Component<Props, State> {

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { market, marketPrices } = this.props;
        const pairDetails = Pairs.get(market, undefined);

        let newOrder;
        if (pairDetails === undefined) {
            newOrder = <div className="order--wrapper">
                <p>Invalid market pair: {market}</p>
            </div>;
        } else {
            const settlement = pairDetails.orderSettlement;
            const marketPrice = pairDetails.fixedPrice ? pairDetails.fixedPrice.toNumber() : marketPrices.get(market, UnknownMarketPrice).price;

            if (!this.atomConnected() && settlement === OrderSettlement.RenExAtomic) {
                newOrder = <AtomicNotice />;
            } else if (market === Pair.BTC_WBTC) {
                newOrder = <WrapBitcoin />;
            } else {
                newOrder = <NewOrderInputs
                    market={market}
                    marketPrice={marketPrice}
                    settlementType={settlement}
                />;
            }
        }

        return (
            <div className="section order">
                <div className="container">
                    <Heading title="Submit an order" />
                    <SelectMarket />
                    {newOrder}
                </div>
            </div >
        );
    }

    private atomConnected(): boolean {
        const { atomConnectionStatus } = this.props;
        return (
            atomConnectionStatus === AtomicConnectionStatus.ConnectedLocked ||
            atomConnectionStatus === AtomicConnectionStatus.ConnectedUnlocked
        );
    }

}

const mapStateToProps = (state: ApplicationData) => ({
    market: state.inputs.market,
    marketPrices: state.marketPrices.marketPrices,
    sdk: state.sdk.sdk,
    atomConnectionStatus: state.trader.atomConnectionStatus,
});

interface Props extends ReturnType<typeof mapStateToProps> {
}

interface State {
}

export const NewOrder = connect(mapStateToProps, null)(NewOrderClass);
