import * as React from "react";

import { List } from "immutable";
import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { getAtomicBalances, getLegacyBalances, updateBalancesHistory } from "../actions/balances/balancesActions";
import { updatePrices } from "../actions/market/marketActions";
import { getHiddenOrders } from "../actions/orderbook/hiddenBookActions";
import { checkOrderStatuses } from "../actions/orderbook/traderBookActions";
import { storeSDKData } from "../actions/sdk/sdkActions";
import { refreshAtomConnectionStatus } from "../actions/settlement/atomicActions";
import { lookForLogout } from "../actions/trader/accountActions";
import { _captureBackgroundException_, _captureInteractionException_ } from "../lib/errors";
import { getPrice, Pairs } from "../lib/market";
import { Wallet } from "../lib/wallets/wallet";
import { ApplicationData } from "../reducers/types";
import { _catch_ } from "./ErrorBoundary";

/**
 * BackgroundTasks is responsible for running background loops like updating
 * balances and prices
 */
class BackgroundTasksClass extends React.Component<Props, State> {

    /**
     * The following timeouts are used to run the various background tasks.
     * setTimeout is used instead of setInterval so the interval between calls
     * includes the time it takes to run the task.
     */
    // tslint:disable: completed-docs
    private readonly setupLoopsTimeout: NodeJS.Timer | undefined;
    private callGetLegacyBalancesTimeout: NodeJS.Timer | undefined;
    private callHiddenOrdersTimeout: NodeJS.Timer | undefined;
    private callUpdatePricesTimeout: NodeJS.Timer | undefined;
    private callLookForLogoutTimeout: NodeJS.Timer | undefined;
    private readonly callConnectToAtomTimeout: NodeJS.Timer | undefined;
    private callUpdateAtomStatusTimeout: NodeJS.Timer | undefined;
    private callGetAtomicBalancesTimeout: NodeJS.Timer | undefined;
    private callCheckOrderStatusesTimeout: NodeJS.Timer | undefined;
    private readonly callCheckRenExExecutionTimeout: NodeJS.Timer | undefined;
    private callUpdateBalancesHistoryTimeout: NodeJS.Timer | undefined;
    // tslint:enable: completed-docs

    public constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
        };
    }

    public async componentDidMount(): Promise<void> {
        // TODO: Remove console.log before deployment
        console.log("Initializing setup loops!");
        this.storeSDKData()
            .then(this.setupLoops)
            .catch(this.setupLoops);
    }

    public componentWillUnmount() {
        // Clear timeouts
        if (this.setupLoopsTimeout) { clearTimeout(this.setupLoopsTimeout); }
        if (this.callGetLegacyBalancesTimeout) { clearTimeout(this.callGetLegacyBalancesTimeout); }
        if (this.callHiddenOrdersTimeout) { clearTimeout(this.callHiddenOrdersTimeout); }
        if (this.callUpdatePricesTimeout) { clearTimeout(this.callUpdatePricesTimeout); }
        if (this.callLookForLogoutTimeout) { clearTimeout(this.callLookForLogoutTimeout); }
        if (this.callConnectToAtomTimeout) { clearTimeout(this.callConnectToAtomTimeout); }
        if (this.callUpdateAtomStatusTimeout) { clearTimeout(this.callUpdateAtomStatusTimeout); }
        if (this.callGetAtomicBalancesTimeout) { clearTimeout(this.callGetAtomicBalancesTimeout); }
        if (this.callCheckOrderStatusesTimeout) { clearTimeout(this.callCheckOrderStatusesTimeout); }
        if (this.callCheckRenExExecutionTimeout) { clearTimeout(this.callCheckRenExExecutionTimeout); }
        if (this.callUpdateBalancesHistoryTimeout) { clearTimeout(this.callUpdateBalancesHistoryTimeout); }
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        return <></>;
    }

    /**
     * setupLoops is called once to start the timeouts
     */
    private readonly setupLoops = () => {
        // See if the user has logged out every 5 seconds
        const callLookForLogout = async () => {
            const { sdk, wallet } = this.props;
            if (sdk.getAddress() && wallet === Wallet.MetaMask) {
                try {
                    // tslint:disable-next-line: await-promise
                    await this.props.actions.lookForLogout(sdk);
                } catch (error) {
                    _captureBackgroundException_(error, { description: "Error in BackgroundTasks.callLookForLogout" });
                }
            }
            this.callLookForLogoutTimeout = setTimeout(callLookForLogout, 5 * 1000);
        };
        callLookForLogout()
            .catch(_captureBackgroundException_);

        // Update the user's balances every 5 seconds
        const callGetLegacyBalances = async () => {
            const { sdk, pendingAlerts } = this.props;
            if (sdk.getAddress()) {
                // tslint:disable-next-line: await-promise
                await this.props.actions.getLegacyBalances(sdk, pendingAlerts);
            }
            this.callGetLegacyBalancesTimeout = setTimeout(callGetLegacyBalances, 5 * 1000);
        };
        callGetLegacyBalances()
            .catch(_captureBackgroundException_);

        // Update the atom connection status every 60 seconds
        const callUpdateAtomStatus = async () => {
            const { sdk } = this.props;
            if (sdk !== null && sdk.getAddress() !== "") {
                try {
                    // tslint:disable-next-line: await-promise
                    await this.props.actions.refreshAtomConnectionStatus(sdk);
                } catch (error) {
                    _captureBackgroundException_(error, { description: "Error in BackgroundTasks.callUpdateAtomicStatus" });
                }
            }
            this.callUpdateAtomStatusTimeout = setTimeout(callUpdateAtomStatus, 60 * 1000);
        };
        callUpdateAtomStatus()
            .catch(_captureBackgroundException_);

        // Update the user's atomic balances every 5 seconds
        const callGetAtomicBalances = async () => {
            const { sdk } = this.props;
            if (sdk.getAddress() && sdk.atom.isConnected()) {
                try {
                    // tslint:disable-next-line: await-promise
                    await this.props.actions.getAtomicBalances(sdk);
                } catch (error) {
                    _captureBackgroundException_(error, { description: "Error in BackgroundTasks.callGetAtomicBalances" });
                }
            }
            this.callGetAtomicBalancesTimeout = setTimeout(callGetAtomicBalances, 5 * 1000);
        };
        callGetAtomicBalances()
            .catch(_captureBackgroundException_);

        // Retrieve new hidden orders from the Orderbook every 30 seconds
        const callHiddenOrders = async () => {
            const { sdk } = this.props;
            try {
                // tslint:disable-next-line: await-promise
                await this.props.actions.getHiddenOrders(sdk);
            } catch (error) {
                _captureBackgroundException_(error, { description: "Error in BackgroundTasks.callHiddenOrders" });
            }
            this.callHiddenOrdersTimeout = setTimeout(callHiddenOrders, 30 * 1000);
        };
        callHiddenOrders()
            .catch(_captureBackgroundException_);

        // Update balance history every 10 seconds
        const callUpdateBalancesHistory = async (count: number) => {
            const { sdk, balanceHistory } = this.props;
            let newCount = count;
            if (sdk.getAddress()) {
                try {
                    // See comment in `callCheckOrderStatuses` on count
                    // tslint:disable-next-line: await-promise
                    await this.props.actions.updateBalancesHistory(sdk, balanceHistory, (newCount % 60 === 1));
                    newCount += 1;
                } catch (error) {
                    _captureBackgroundException_(error, { description: "Error in BackgroundTasks.callUpdateBalancesHistory" });
                }
            }
            this.callUpdateBalancesHistoryTimeout = setTimeout(async () => callUpdateBalancesHistory(newCount), 10 * 1000);
        };
        callUpdateBalancesHistory(0)
            .catch(_captureBackgroundException_);

        // Retrieve market prices every minute
        const callUpdatePrices = async () => {
            try {
                for (const pair of Pairs.valueSeq().toArray()) {
                    const [price, percentChange] = await getPrice(pair.quoteToken, pair.baseToken);
                    this.props.actions.updatePrices({ price, percentChange, pair: pair.code });
                }
            } catch (error) {
                _captureBackgroundException_(error, { description: "Error in BackgroundTasks.callUpdatePrices" });
            }
            this.callUpdatePricesTimeout = setTimeout(callUpdatePrices, 60 * 1000);
        };
        callUpdatePrices()
            .catch(_captureBackgroundException_);

        // Check for status updates every 5 seconds
        const callCheckOrderStatuses = async (count: number) => {
            const { sdk, traderOrders } = this.props;
            let newCount = count;
            if (sdk.getAddress()) {
                try {
                    // Every 120 times (10 minutes) including the second time,
                    // check all orders. The full check is done on the second
                    // call so that statuses likely to change are checked
                    // sooner (in the first call) but the full check is still
                    // run early on (in the second call).
                    // Successive full checks are called every 10 minutes in
                    // case the window is not being refreshed.
                    // tslint:disable-next-line: await-promise
                    await this.props.actions.checkOrderStatuses(sdk, traderOrders, (newCount % 120 === 1));
                    newCount += 1;
                } catch (error) {
                    _captureBackgroundException_(error, { description: "Error in BackgroundTasks.callCheckOrderStatuses" });
                }
            }
            this.callCheckOrderStatusesTimeout = setTimeout(async () => callCheckOrderStatuses(newCount), 5 * 1000);
        };
        callCheckOrderStatuses(0)
            .catch(_captureBackgroundException_);
    }

    /**
     * storeSDKData fetches variables from the SDK including supported tokens,
     * supported markets and fee constants.
     */
    private readonly storeSDKData = async (): Promise<void> => {
        const { sdk } = this.props;
        const [supportedTokens, supportedAtomicTokens, darknodeFeePercent, wrappingFeePercent, minEthTradeVolume, supportedMarkets] = await Promise.all([
            sdk.fetchSupportedTokens(),
            sdk.fetchSupportedAtomicTokens(),
            sdk.fetchDarknodeFeePercent(),
            sdk.fetchWrappingFeePercent(),
            sdk.fetchMinEthTradeVolume(),
            sdk.fetchMarkets(),
        ]);

        this.props.actions.storeSDKData({
            supportedTokens: List(supportedTokens),
            supportedAtomicTokens: List(supportedAtomicTokens),
            darknodeFeePercent,
            wrappingFeePercent,
            minEthTradeVolume,
            supportedMarkets: List(supportedMarkets),
        });
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    balanceHistory: state.balanceHistory.balanceHistory,
    pendingAlerts: state.alert.pendingAlerts,
    sdk: state.sdk.sdk,
    traderOrders: state.traderBook.traderOrders,
    wallet: state.trader.wallet,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        checkOrderStatuses,
        getAtomicBalances,
        getLegacyBalances,
        getHiddenOrders,
        lookForLogout,
        refreshAtomConnectionStatus,
        storeSDKData,
        updateBalancesHistory,
        updatePrices,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
}

interface State {
}

export const BackgroundTasks = connect(mapStateToProps, mapDispatchToProps)(BackgroundTasksClass);
