import * as React from "react";

import TransportU2F from "@ledgerhq/hw-transport-u2f";
import createLedgerSubprovider from "@ledgerhq/web3-subprovider";
import Web3 from "web3";
import ProviderEngine from "web3-provider-engine";
import FetchSubprovider from "web3-provider-engine/subproviders/fetch";
import NonceSubprovider from "web3-provider-engine/subproviders/nonce-tracker";

import { HttpProvider, Provider } from "web3/providers";

import { setPopup } from "../../actions/popup/popupActions";
import { LedgerPopup } from "../../components/popups/LedgerPopup";
import { store } from "../../index";
import { INFURA_URL, networkData } from "../../lib/network";
import { ErrorCanceledByUser, PopupPromise, WalletDetail } from "../../lib/wallets/wallet";
import { getAccounts } from "../../lib/web3";

interface ProviderEngine extends HttpProvider {
    /**
     * Starts the engine's block tracking
     */
    start(): void;

    /**
     * Stops the engine's block tracking
     */
    stop(): void;

    /**
     * Adds a provider to the engine
     */
    addProvider(provider: Provider): void;
}

const createProvider = (derivation: string): ProviderEngine => {
    const engine: ProviderEngine = new ProviderEngine();
    const getTransport = () => TransportU2F.create();

    let newDerivation = derivation;

    // Remove m/ from start
    if (newDerivation.slice(0, 2) === "m/") {
        newDerivation = newDerivation.slice(2);
    }

    const ledger = createLedgerSubprovider(getTransport, {
        networkId: networkData.ethNetworkId,
        accountsLength: 5,
        path: newDerivation,
    });
    engine.addProvider(ledger);
    engine.addProvider(new NonceSubprovider());
    engine.addProvider(new FetchSubprovider({ rpcUrl: INFURA_URL }));
    engine.start();

    return engine;
};

const checkConnection = async (args: {
    /**
     * The BIP32 derivation string
     */
    derivation: string;
}): Promise<[ProviderEngine, string[]]> => {
    const engine = createProvider(args.derivation);
    const accounts = await getAccounts(new Web3(engine));
    if (!accounts || accounts.length === 0) {
        throw new Error("Unable to read accounts.");
    }

    return [engine, accounts];
};

export const Ledger: WalletDetail = {
    name: "Ledger",
    slug: "ledger",
    description: "Sign in using your Ledger hardware wallet",
    enabled: true,
    // create a web3 with the ledger device
    getWeb3Provider: async (address?: string) => PopupPromise<[Provider, string[] | null]>(async (resolve, reject) => {

        store.dispatch(setPopup(
            {
                popup: <LedgerPopup resolve={resolve} reject={reject} address={address} checkConnection={checkConnection} />,
                onCancel: () => { reject(new Error(ErrorCanceledByUser)); }
            }
        ));
    }),
};
