import * as React from "react";

import { ControlProps } from "react-select/lib/components/Control";
import { OptionProps } from "react-select/lib/components/Option";
import { SingleValueProps } from "react-select/lib/components/SingleValue";
import { TokenDetails, UnknownToken } from "../lib/market";

interface OptionType {
    label: string;
    value: string;
    // tslint:disable-next-line: no-any no-reserved-keywords
    symbol: any;
    name: string;
    orderSettlement?: string;
}

export const CustomControl = <X extends OptionType>(props: ControlProps<X>) => {
    const { children, cx, getStyles, className, isDisabled, isFocused, innerRef, innerProps } = props;
    // tslint:disable-next-line: no-any
    const { menuIsOpen, emotion } = (props as any);
    return (
        <div
            ref={innerRef}
            className={`${(cx(emotion.css(getStyles("control", props)), {
                control: true,
                "control--is-disabled": isDisabled,
                "control--is-focused": isFocused,
                "control--menu-is-open": menuIsOpen
            }, className) || "")} Select-control`}
            {...innerProps}
        >
            {children}
        </div>
    );
};

export const CustomValue = <X extends OptionType>(props: SingleValueProps<X>) => {
    const { className, cx, getStyles, isDisabled, innerProps, children } = props;
    // tslint:disable-next-line: no-any
    const { emotion } = (props as any);

    const option = props.data;

    const filename = TokenDetails.get(option.value, UnknownToken).icon;

    // tslint:disable-next-line:non-literal-require
    const image = require(`../tokens/${filename}`);

    return (
        <div
            className={`${cx(
                // tslint:disable-next-line: no-any
                emotion.css(getStyles("singleValue", props)),
                {
                    "single-value": true,
                    "single-value--is-disabled": isDisabled
                },
                className
            )} Select-value`}
            {...innerProps}
        >
            <img alt="" role="presentation" src={image} />
            {children}
        </div>
    );
};

export const CustomOption = <X extends OptionType>(props: OptionProps<X>) => {
    const { children, className, cx, getStyles, isDisabled, isFocused, isSelected, innerRef, innerProps } = props;
    // tslint:disable-next-line: no-any
    const { emotion } = (props as any);

    const option = props.data;

    const filename = TokenDetails.get(option.value, UnknownToken).icon;

    // tslint:disable-next-line:non-literal-require
    const image = require(`../tokens/${filename}`);
    return (
        <div
            ref={innerRef}
            className={`${cx(
                emotion.css(getStyles("option", props)),
                {
                    option: true,
                    "option--is-disabled": isDisabled,
                    "option--is-focused": isFocused,
                    "option--is-selected": isSelected,
                },
                className
            ) || ""} Select--option`}
            {...innerProps}
        >
            <img role="presentation" alt="" src={image} />
            {children}
        </div >
    );
};
