import React from "react";

import BigNumber from "bignumber.js";

import { FieldProps } from "formik";

import { InfoLabel } from "./InfoLabel";

interface Props {
    ticker: string;
    title: React.ReactNode;
    disabled?: boolean;
    hint?: React.ReactNode;
    notice?: React.ReactNode;
    stepFunction?(value: BigNumber): string;
    handleChange?(e: React.ChangeEvent<HTMLInputElement>): void;
    handleBlur?(e: React.ChangeEvent<HTMLInputElement>): void;
}

export class NewOrderInputField extends React.Component<FieldProps<HTMLInputElement> & Props, HTMLInputElement> {
    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const {
            form: { errors, dirty },
            title, ticker, disabled, hint, handleChange, handleBlur, notice
        } = this.props;
        const { onBlur, onChange, ...field } = this.props.field;

        return (
            <div className="order--input-wrapper">
                <label>
                    <div className="order--title">
                        <span>{title}</span>
                        {hint && <InfoLabel>{hint}</InfoLabel>}
                    </div>
                    <span className={`order--input-item${disabled ? " disabled" : ""}`}>
                        <input
                            {...field}
                            type="number"
                            step={this.calculateStep()}
                            disabled={disabled}
                            placeholder="0"
                            min={0}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        />
                        <span>{ticker}</span>
                    </span>
                </label>
                {!!errors[field.name] && dirty && (
                    <p className="order--warning">
                        {errors[field.name]}
                    </p>
                )}
                {!!notice && !errors[field.name] && (
                    <p className="order--message">
                        {notice}
                    </p>
                )}
            </div>
        );
    }

    private calculateStep(): string {
        const { stepFunction, field } = this.props;
        if (stepFunction) {
            return stepFunction(new BigNumber(field.value || "0"));
        }
        return "";
    }

}
