import * as React from "react";

import BigNumber from "bignumber.js";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { updateAtomConnectionStatus } from "../actions/settlement/atomicActions";
import { _captureInteractionException_ } from "../lib/errors";
import { TokenDetails, UnknownToken } from "../lib/market";
import { ApplicationData } from "../reducers/types";
import { InfoLabel } from "./InfoLabel";
import { Loading } from "./Loading";

class WrapBitcoinClass extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.state = {
            error: null,
            linkPending: false,
            inputValue: "0",
            wrap: true,
            isSubmitting: false,
        };
    }

    // tslint:disable: jsx-no-lambda
    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { wrappingFeePercent, usableAtomicBalances } = this.props;
        const btcTokenDetails = TokenDetails.get("BTC", UnknownToken);
        const wbtcTokenDetails = TokenDetails.get("WBTC", UnknownToken);
        const { isSubmitting, wrap, error } = this.state;
        let amount = new BigNumber(this.state.inputValue);
        if (amount.isNaN() || !amount.isFinite()) {
            amount = new BigNumber(0);
        }
        const spendToken = wrap ? btcTokenDetails : wbtcTokenDetails;
        const spendBalance = usableAtomicBalances.get(spendToken.symbol) || new BigNumber(0);
        const receiveToken = wrap ? wbtcTokenDetails : btcTokenDetails;
        const feeAmount = amount.times(wrappingFeePercent);

        return <div className="order--wrapper order--wrapper--tabbed">
            <ul className="order--tabs">
                <li
                    className={wrap ? "active" : ""}
                    role="tab"
                    onClick={this.enableWrap}
                >
                    <span>Wrap BTC</span>
                </li>
                <li
                    className={!wrap ? "active" : ""}
                    role="tab"
                    onClick={this.disableWrap}
                >
                    <span>Unwrap WBTC</span>
                </li>
            </ul>
            <form onSubmit={this.handleSubmit}>
                <div className="order--inputs">
                    <div className="order--input-wrapper">
                        <label>
                            <div className="order--title">
                                <span>Spend</span>
                            </div>
                            <span className="order--input-item">
                                <input min="0" step=".00000001" type="number" value={this.state.inputValue} onChange={this.handleChange} />
                                <span>{spendToken.symbol}</span>
                            </span>
                        </label>
                        {spendBalance.lt(amount) ?
                            <p className="order--warning">Insufficient {spendToken.symbol} balance</p>
                            : (
                                !feeAmount.isEqualTo(0) &&
                                <p className="order--message">You will spend {new BigNumber(this.state.inputValue).plus(feeAmount).toFixed()} {wrap ? "BTC" : "WBTC"} after fees.</p>
                            )

                        }
                    </div>
                    <div className="order--input-wrapper">
                        <label>
                            <div className="order--title">
                                <span>Receive</span>
                            </div>
                            <span className="order--input-item">
                                <input min="0" step=".00000001" type="number" value={this.state.inputValue} onChange={this.handleChange} disabled />
                                <span>{receiveToken.symbol}</span>
                            </span>
                        </label>
                    </div>
                </div>
                {!feeAmount.isEqualTo(0) &&
                    <div>
                        <span>Fee: {feeAmount.toFixed()} {spendToken.symbol}</span>
                        <InfoLabel>Fees for {wrap ? "wrapping BTC" : "unwrapping WBTC"} are set to {wrappingFeePercent.times(100).toFixed()}% of the order volume.</InfoLabel>
                    </div>
                }
                <div className="order--footer">
                    <button className={`order--button ${isSubmitting ? "submitting" : ""}`} type="submit" disabled={this.checkDisable()}>
                        <span>
                            {isSubmitting ?
                                <Loading />
                                : (
                                    wrap ? "Wrap BTC" : "Unwrap WBTC"
                                )
                            }
                        </span>
                    </button>
                </div>
                {error && error !== "User rejected the swap" &&
                    <div>
                        <p className="order--warning">{error}</p>
                    </div>
                }
            </form>
        </div >;
    }

    private readonly enableWrap = () => { this.setState({ wrap: true }); };
    private readonly disableWrap = () => { this.setState({ wrap: false }); };

    private readonly checkDisable = (): boolean => {
        const { usableAtomicBalances } = this.props;
        const { wrap, isSubmitting } = this.state;
        const value = new BigNumber(this.state.inputValue);
        const spendToken = wrap ? "BTC" : "WBTC";
        const usable: BigNumber = usableAtomicBalances.get(spendToken) || new BigNumber(0);
        const valid = value.gt(0) && value.lte(usable);

        return !valid || isSubmitting;
    }

    private readonly handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        const { wrap, inputValue } = this.state;
        this.setState({ isSubmitting: true, error: null });
        event.preventDefault();
        // Buying WBTC means wrapping Bitcoin
        const swap = (wrap) ? this.wrap() : this.unwrap();

        return swap
            // tslint:disable-next-line: no-any
            .then((res: any) => {
                // TODO: Remove console.log before deployment
                console.log(res);
                this.setState({ isSubmitting: false });
            })
            .catch((error: Error) => {
                _captureInteractionException_(error, {
                    description: "Error in WrapBitcoin.handleSubmit",
                    shownToUser: "In WrapBitcoin as message box",
                    wrap,
                    inputValue,
                });
                this.setState({ error: error.message, isSubmitting: false });
            });
    }

    // tslint:disable-next-line:no-any
    private readonly wrap = async (): Promise<any> => {
        const { sdk } = this.props;

        return sdk.atom.wrap(this.state.inputValue, "BTC");
    }

    // tslint:disable-next-line:no-any
    private readonly unwrap = async (): Promise<any> => {
        const { sdk } = this.props;

        return sdk.atom.unwrap(this.state.inputValue, "WBTC");
    }

    private readonly handleChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
        let inputValue;
        inputValue = (event.target as HTMLInputElement).value;
        if (inputValue.indexOf("e") >= 0) {
            inputValue = new BigNumber(inputValue).toFixed();
        }
        this.setState({ inputValue });
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    sdk: state.sdk.sdk,
    atomConnectionStatus: state.trader.atomConnectionStatus,
    usableAtomicBalances: state.trader.usableAtomicBalances,
    wrappingFeePercent: state.sdk.wrappingFeePercent,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        updateAtomConnectionStatus,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
}

interface State {
    error: string | null;
    linkPending: boolean;
    inputValue: string;
    wrap: boolean;
    isSubmitting: boolean;
}

export const WrapBitcoin = connect(mapStateToProps, mapDispatchToProps)(WrapBitcoinClass);
