import { ActionType, getType } from "typesafe-actions";

import * as atomicActions from "../../actions/settlement/atomicActions";
import * as accountActions from "../../actions/trader/accountActions";
import * as balanceActions from "../../actions/trader/balanceActions";
import * as migrationActions from "../../actions/trader/migrationActions";
import * as termsActions from "../../actions/trader/termsActions";
import * as walletActions from "../../actions/trader/walletActions";

import { TraderData } from "../../reducers/types";

type AccountAction = ActionType<typeof accountActions>;
type TermsAction = ActionType<typeof termsActions>;
type MigrationActions = ActionType<typeof migrationActions>;
type BalanceAction = ActionType<typeof balanceActions>;
type WalletAction = ActionType<typeof walletActions>;
type AtomicAction = ActionType<typeof atomicActions>;

export const traderReducer = (state: TraderData = new TraderData(), action: AccountAction | TermsAction | MigrationActions | BalanceAction | WalletAction | AtomicAction) => {
    switch (action.type) {
        case getType(accountActions.storeAddress):
            return state.set("address", action.payload);
        case getType(accountActions.storeURL):
            return state.set("url", action.payload);

        // Legacy balances
        case getType(balanceActions.updateLegacyBalance):
            return state.set("legacyBalances", state.legacyBalances.set(action.payload.token, action.payload.balance));
        case getType(balanceActions.updateLegacyBalances):
            return state.set("legacyBalances", action.payload.balances);

        // Legacy usable balances
        case getType(balanceActions.updateUsableLegacyBalance):
            return state.set("usableLegacyBalances", state.usableLegacyBalances.set(action.payload.token, action.payload.usableBalance));
        case getType(balanceActions.updateUsableLegacyBalances):
            return state.set("usableLegacyBalances", action.payload.usableBalances);

        // Account balance
        case getType(balanceActions.updateAccountLegacyBalance):
            return state.set("accountLegacyBalances", state.accountLegacyBalances.set(action.payload.token, action.payload.accountBalance));
        case getType(balanceActions.updateAccountLegacyBalances):
            return state.set("accountLegacyBalances", action.payload.accountBalances);

        // Usable Atomic Action
        case getType(balanceActions.updateUsableAtomicBalances):
            return state.set("usableAtomicBalances", action.payload.usableAtomicBalances);

        // Atomic Balance
        case getType(balanceActions.updateAtomicBalances):
            return state.set("atomicBalances", action.payload.atomicBalances);
        case getType(balanceActions.updateAtomicAddresses):
            return state.set("atomicAddresses", action.payload.atomicAddresses);
        case getType(atomicActions.updateAtomConnectionStatus):
            return state.set("atomConnectionStatus", action.payload.atomConnectionStatus);

        // Terms
        case getType(termsActions.agreeToTerms):
            return state.set("agreedToTerms", action.payload.agreedToTerms);

        // Migration
        case getType(migrationActions.seenMigration):
            return state.set("seenMigration", action.payload.seenMigration);

        // Wallet
        case getType(walletActions.storeWallet):
            return state.set("wallet", action.payload.wallet);

        default:
            return state;
    }
};
