import * as React from "react";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { Link } from "react-router-dom";
import { bindActionCreators, Dispatch } from "redux";

import { Heading } from "./Heading";

import { _captureInteractionException_ } from "../lib/errors";
import { ApplicationData } from "../reducers/types";
import { SubmittedTradesTable } from "./SubmittedTradesTable";

// This is the number of orders that will be displayed if the 'recent' prop
// is set.
export const RECENT_AMOUNT = 5;

class SubmittedTradesClass extends React.Component<Props, State> {
    public constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
        };
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { recent } = this.props;
        const { traderOrders } = this.props;

        const title = `Your ${recent ? "latest " : ""}submitted orders`;

        return (
            <div className="section trades">
                <div className="container">
                    <Heading title={title} />
                    <SubmittedTradesTable amountToShow={recent ? RECENT_AMOUNT : undefined} />
                    {recent && traderOrders && traderOrders.size - RECENT_AMOUNT > 0 &&
                        <p><Link to="/orders">View {traderOrders.size - RECENT_AMOUNT} more</Link></p>
                    }
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    traderOrders: state.traderBook.traderOrders,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
    recent?: boolean;
}

interface State {
}

export const SubmittedTrades = connect(mapStateToProps, mapDispatchToProps)(SubmittedTradesClass);
