import * as React from "react";

import { SimpleConsole } from "@renex/renex";

import { SetPopupPayload } from "../../actions/popup/popupActions";
import { _captureInteractionException_ } from "../../lib/errors";
import { ErrorCanceledByUser } from "../../lib/wallets/wallet";

/**
 * ApprovalPopup is a popup component that prompts the user to approve a
 * series of Ethereum transactions
 */
class ApprovalPopup extends React.Component<Props, State> {

    /**
     * _isMounted is used to prevent setting state after the component has been
     * unmounted.
     */
    private _isMounted: boolean = false;

    constructor(props: Props) {
        super(props);
        this.state = {
            error: null,
            logCount: 0,
        };
    }

    public componentWillUnmount() {
        this._isMounted = false;
    }

    public async componentDidMount() {
        this._isMounted = true;

        const { call } = this.props;

        await call()
            .catch((error) => {

                let errorMessage;
                if (error.message.match("User denied transaction signature")) {
                    errorMessage = ErrorCanceledByUser;
                } else {
                    _captureInteractionException_(error, {
                        description: "Error in ApprovalPopup.call()",
                        shownToUser: this._isMounted ? "In ApprovalPopup message box" : "No, unmounted",
                    });
                    errorMessage = error.message || error;
                }

                if (this._isMounted) {
                    this.setState({ error: errorMessage });
                }
            });
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { error } = this.state;
        const { closePopup } = this.props;

        let title = <h2>Creating transaction...</h2>;
        let message = <p>Please wait. Do not close this window.</p>;
        if (error) {
            switch (error) {
                case ErrorCanceledByUser:
                    title = <h2>Transaction canceled</h2>;
                    message = <p />;
                    break;
                default:
                    title = <h2 className="red">Unable to complete transaction</h2>;
                    message = <p>{error}</p>;
            }
        }
        return <div className="popup approve">
            {title}
            {message}
            {error &&
                <div className="popup--buttons">
                    <button onClick={closePopup}>Close</button>
                </div>
            }
        </div>;
    }
}

interface Props {
    /**
     * The function to call after the user has approved the transaction.
     */
    call(): Promise<void>;

    /**
     * A callback to call if the user cancels the transaction.
     */
    closePopup(): void;
}

interface State {
    error: string | null;
    logCount: number;
}

export const newApprovalPopup = (call: (simpleConsole?: SimpleConsole) => Promise<void>, onCancelAction: () => void): SetPopupPayload => ({
    popup: <ApprovalPopup closePopup={onCancelAction} call={call} />,
    dismissible: false,
    onCancel: onCancelAction,
});
