import * as React from "react";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { setMarket } from "../../actions/inputs/inputsActions";
import { BalanceBanner } from "../../components/BalanceBanner";
import { ExecutedTrades } from "../../components/ExecutedTrades";
import { Header } from "../../components/Header";
import { HiddenOrderbook } from "../../components/HiddenOrderbook";
import { MarketBanner } from "../../components/MarketBanner";
import { NewOrder } from "../../components/NewOrder";
import { SubmittedTrades } from "../../components/SubmittedTrades";
import { _captureInteractionException_ } from "../../lib/errors";
import { Pairs } from "../../lib/market";
import { _catch_ } from "../ErrorBoundary";

/**
 * Home is a page whose principal component allows users to open orders. The
 * page also displays the hidden orderbook.
 */
class ExchangeClass extends React.Component<Props, Exchange> {
    public componentDidMount(): void {
        try {
            // Set market pair based on URL
            const params = window.location.href.split("/");
            const fstCode = params[4];
            const sndCode = params[5];
            if (fstCode && sndCode) {
                for (const pair of Pairs.valueSeq().toArray()) {
                    if (fstCode.toUpperCase() === pair.quoteToken) {
                        if (sndCode.toUpperCase() === pair.baseToken) {
                            this.props.actions.setMarket({ market: pair.code });
                            break;
                        }
                    }
                }
            }
        } catch (error) {
            _captureInteractionException_(error, {
                description: "Error in Exchange.componentDidMount",
                shownToUser: "No",
            });
        }
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render = (): React.ReactNode =>
        <div className="exchange">
            {_catch_(<MarketBanner />)}
            {_catch_(<Header withMenu={true} />)}
            {_catch_(<BalanceBanner />)}

            <div className="content">
                {_catch_(<NewOrder />)}
                {_catch_(<SubmittedTrades recent />)}
                {_catch_(<ExecutedTrades recent />)}
                {_catch_(<HiddenOrderbook />)}
            </div>
        </div>
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        setMarket,
    }, dispatch)
});

interface Props extends ConnectedReturnType<typeof mapDispatchToProps> {
}

interface Exchange {
}

export const Exchange = connect(null, mapDispatchToProps)(ExchangeClass);
