import BigNumber from "bignumber.js";

import RenExSDK, { TokenCode } from "@renex/renex";

import { OrderedMap } from "immutable";
import { Dispatch } from "redux";
import { createStandardAction } from "typesafe-actions";

import { setAlert } from "../../actions/alert/alertActions";
import { getLegacyBalances, storeBalanceActions } from "../../actions/balances/balancesActions";
import { clearPopup, setPopup } from "../../actions/popup/popupActions";
import { newApprovalPopup } from "../../components/popups/ApprovalPopup";
import { _captureInteractionException_ } from "../../lib/errors";
import { Alert } from "../../reducers/types";
import { BalanceActionRecord } from "../../reducers/typesSdk";

export const ETH_REQUIRED = "0.1";

const FAILED_WITHDRAWAL_ERROR = "Unable to withdraw funds";

// Legacy balances
export const updateLegacyBalance = createStandardAction("UPDATE_LEGACY_BALANCE")<{ token: TokenCode; balance: BigNumber | null }>();
export const updateLegacyBalances = createStandardAction("UPDATE_LEGACY_BALANCES")<{ balances: OrderedMap<TokenCode, BigNumber | null> }>();
export const updateUsableLegacyBalances = createStandardAction("UPDATE_USABLE_LEGACY_BALANCES")<{ usableBalances: OrderedMap<TokenCode, BigNumber | null> }>();
export const updateUsableLegacyBalance = createStandardAction("UPDATE_USABLE_LEGACY_BALANCE")<{ token: TokenCode; usableBalance: BigNumber | null }>();
export const updateAccountLegacyBalance = createStandardAction("UPDATE_ACCOUNT_LEGACY_BALANCE")<{ token: TokenCode; accountBalance: BigNumber | null }>();
export const updateAccountLegacyBalances = createStandardAction("UPDATE_ACCOUNT_LEGACY_BALANCES")<{ accountBalances: OrderedMap<TokenCode, BigNumber | null> }>();

// Atomic Balance
export const updateAtomicBalances = createStandardAction("UPDATE_ATOMIC_BALANCES")<{ atomicBalances: OrderedMap<TokenCode, BigNumber | null> }>();
export const updateAtomicAddresses = createStandardAction("UPDATE_ATOMIC_ADDRESSES")<{ atomicAddresses: OrderedMap<TokenCode, string> }>();
export const updateUsableAtomicBalances = createStandardAction("UPDATE_USABLE_ATOMIC_BALANCES")<{ usableAtomicBalances: OrderedMap<TokenCode, BigNumber | null> }>();

export const WITHDRAW_DELAY = 5;

export const withdraw = (
    sdk: RenExSDK,
    balances: OrderedMap<TokenCode, BigNumber | null>,
    usableBalances: OrderedMap<TokenCode, BigNumber | null>,
    amount: string,
    currencyCode: TokenCode,
) => async (dispatch: Dispatch) => {
    if (amount === null || balances === null || currencyCode === null || parseFloat(amount) <= 0 || sdk === null) {
        throw new Error(FAILED_WITHDRAWAL_ERROR);
    }

    const amountBigNumber = new BigNumber(amount);
    if (amountBigNumber.lte(new BigNumber(0))) {
        dispatch(setAlert({
            alert: new Alert({ message: "Please enter valid input" })
        }));
        return;
    }

    // Check if user has sufficient balance to withdraw.
    const balance = usableBalances.get(currencyCode, undefined);
    if (balance === undefined || balance === null) {
        throw new Error("Unable to read balance of token while withdrawing");
    }
    if (amountBigNumber.gt(balance)) {
        throw new Error("Insufficient balance while withdrawing");
    }

    return new Promise((resolve: () => void) => {

        const close = () => {
            dispatch(clearPopup());
            resolve();
        };
        const transactionCall = async () => {
            return sdk.withdraw(amountBigNumber, currencyCode, { awaitConfirmation: false })
                .then(async withdrawResult => {
                    const { balanceAction } = withdrawResult;
                    const balanceActionRecord = new BalanceActionRecord(balanceAction);
                    dispatch(storeBalanceActions([balanceActionRecord]));
                    // Reload balances before closing popup
                    await getLegacyBalances(sdk)(dispatch);
                    close();
                });
        };

        dispatch(setPopup(newApprovalPopup(transactionCall, close)));
    });
};
