import { createStandardAction } from "typesafe-actions";

import { Pair } from "../../lib/market";
import { OrderInputs } from "../../reducers/types";

/**
 * Updates the current market (token pair, e.g. ETH/REN)
 */
export const setMarket = createStandardAction("SET_MARKET")<{
    /**
     * The market being set.
     */
    market: Pair;
}>();

/**
 * Updates the NewOrder inputs
 */
export const setOrder = createStandardAction("SET_ORDER")<{
    /**
     * The order inputs being set.
     */
    orderInputs: OrderInputs;
}>();
