import * as React from "react";

import { List } from "immutable";
import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { clearPopup, setPopup } from "../actions/popup/popupActions";
import { WalletItem } from "../components/WalletItem";
import { Wallet, WalletList } from "../lib/wallets/wallet";

/**
 * Wallets displays a selection of wallets (MetaMask, Ledger, etc.)
 */
class WalletsClass extends React.Component<Props> {
    public constructor(props: Props, context: object) {
        super(props, context);
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        return (
            <div className="section wallets">
                <div className="container">
                    <div className="wallets--wrapper">
                        {this.listWallets()}
                    </div>
                    <p className="wallets--disclaimer">
                        Your trades and transactions are stored locally within your browser.
                        {/*By proceeding, you agree to the <span onClick={this.handleClick}>Terms &amp; Conditions</span>.*/}
                    </p>
                </div>
            </div>
        );
    }

    private readonly listWallets = (): List<React.ReactNode> => {
        return WalletList.map((wallet: Wallet, i: number) => {
            return (
                <WalletItem key={i} wallet={wallet} />
            );
        });
    }
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        clearPopup,
        setPopup,
    }, dispatch)
});

interface Props extends ConnectedReturnType<typeof mapDispatchToProps> {
}

export const Wallets = connect(null, mapDispatchToProps)(WalletsClass);
