import * as React from "react";

import { AtomicConnectionStatus } from "@renex/renex";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { Loading } from "./Loading";

import { authorizeSwapper, updateAtomConnectionStatus } from "../actions/settlement/atomicActions";
import { ApplicationData } from "../reducers/types";

/**
 * AtomicNotice
 */
class AtomicNoticeClass extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.state = {
            linkError: null,
            linkPending: false,
        };
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { atomConnectionStatus } = this.props;
        const { linkPending, linkError } = this.state;

        const reconnect = <button onClick={this.retryConnect} disabled={linkPending}>
            {linkPending ? <Loading /> : "Retry"}
        </button>;

        let inner;

        switch (atomConnectionStatus) {
            case AtomicConnectionStatus.NotConnected:
                inner = <>
                    <h2>Could not detect Swapperd</h2>
                    <p>The Swapperd software is required for trading atomic swap pairs and viewing atomic balances. You can download the software using the links below:</p>
                    <ul>
                        <li><a href="https://releases.republicprotocol.com/swapperd/Swapperd.exe">Swapperd for Windows</a></li>
                        <li><a href="https://releases.republicprotocol.com/swapperd/Swapperd.dmg">Swapperd for MacOS</a></li>
                    </ul>
                    {linkError && <p className="red">{linkError}</p>}
                    {reconnect}
                </>;
                break;
            case AtomicConnectionStatus.NotAuthorized:
                inner = <>
                    <p>Your Swapperd has not been configured for this wallet. Please update your Swapperd configuration.</p>
                    {linkError && <p className="red">{linkError}</p>}
                    {reconnect}
                </>;
                break;
            case AtomicConnectionStatus.AtomNotAuthorized:
                inner = <>
                    <p>Your Swapperd needs to be authorized to action orders on your behalf.</p>
                    {linkError && <p className="red">{linkError}</p>}
                    <button onClick={this.retryAuthorize} disabled={linkPending}>
                        {linkPending ? <Loading /> : linkError ? "Retry" : "Authorize"}
                    </button>
                </>;
                break;
            case AtomicConnectionStatus.ChangedSwapper:
                inner = <>
                    <p>Your Swapperd details have changed. After ensuring your Swapperd details are correct and have been correctly configured correctly, click the Reconnect button.</p>
                    {linkError && <p className="red">{linkError}</p>}
                    {reconnect}
                </>;
                break;
            case AtomicConnectionStatus.InvalidSwapper:
                inner = <>
                    <p>Unable to verify authenticity of Swapperd. Please try updating the application.</p>
                    {linkError && <p className="red">{linkError}</p>}
                    {reconnect}
                </>;
                break;
            case AtomicConnectionStatus.ConnectedLocked:
            case AtomicConnectionStatus.ConnectedUnlocked:
                inner = <>
                    <p>Successfully connected.</p>
                </>;
                break;
            default:
                inner = <p>Unknown Swapperd connection status: {atomConnectionStatus}</p>;
        }

        return <div className="order--wrapper">
            {inner}
        </div>;
    }

    /**
     * retryAuthorize attempts to authorize Swapperd.
     */
    private readonly retryAuthorize = async () => {
        const { sdk } = this.props;

        // tslint:disable-next-line:no-any
        await this.handleLinkAtom(() => (this.props.actions.authorizeSwapper as any)(sdk));
    }

    /**
     * retryConnect attempts to reconnect to Swapperd.
     */
    private readonly retryConnect = async () => {
        const { sdk } = this.props;
        await this.handleLinkAtom(sdk.atom.resetStatus);
    }

    /**
     * handleLinkAtom is called by retryAuthorize or retryConnect. It takes in
     * a Swapperd action and sets the states for linkPending and linkError
     * depending on the outcome of the action.
     */
    private readonly handleLinkAtom = async (atomAction: () => Promise<AtomicConnectionStatus>) => {
        const { actions } = this.props;
        this.setState({
            linkError: null,
            linkPending: true,
        });

        return atomAction()
            .then(atomConnectionStatus => {
                this.setState({
                    linkPending: false,
                });
                actions.updateAtomConnectionStatus({ atomConnectionStatus });
            })
            .catch((error) => {
                this.setState({
                    linkError: error,
                    linkPending: false,
                });
            });
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    sdk: state.sdk.sdk,
    atomConnectionStatus: state.trader.atomConnectionStatus,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        authorizeSwapper,
        updateAtomConnectionStatus,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
}

interface State {
    /**
     * linkError specifies if an error occurred while connecting to Swapperd.
     */
    linkError: string | null;

    /**
     * linkPending specifies if an attempt to connect to Swapperd is in
     * progress.
     */
    linkPending: boolean;
}

export const AtomicNotice = connect(mapStateToProps, mapDispatchToProps)(AtomicNoticeClass);
