import * as React from "react";

import BigNumber from "bignumber.js";

import { TokenDetail } from "../lib/market";

interface Props {
    spendToken: TokenDetail;
    receiveToken: TokenDetail;
    spendVolume?: BigNumber;
    receiveVolume?: BigNumber;
    leftArrow?: boolean;
}

interface State {
}

export class TradeTokenBanner extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { spendToken, receiveToken, spendVolume, receiveVolume, leftArrow } = this.props;
        // tslint:disable-next-line:non-literal-require
        const spendTokenIcon = require(`../tokens/${spendToken.icon}`);
        // tslint:disable-next-line:non-literal-require
        const receiveTokenIcon = require(`../tokens/${receiveToken.icon}`);

        return <div className="open--tokens">
            <div>
                <img alt="" role="presentation" src={spendTokenIcon} />
                {spendVolume && <p>{spendVolume.toFixed()} {spendToken.symbol}</p>}
            </div>
            <div className="open--arrow">{leftArrow ? "←" : "→"}</div>
            <div>
                <img alt="" role="presentation" src={receiveTokenIcon} />
                {receiveVolume && <p>{receiveVolume.toFixed()} {receiveToken.symbol}</p>}
            </div>
        </div>;
    }
}
