import RenExSDK, { Order } from "@renex/renex";

import { List } from "immutable";
import { Dispatch } from "redux";
import { createStandardAction } from "typesafe-actions";

export const setHiddenOrders = createStandardAction("SET_HIDDEN_ORDERS")<{
    hiddenOrders: List<Order>;
}>();

/**
 * Retrieves hidden orders with an index greater than or equal to startingIndex
 */
export const getHiddenOrders = (sdk: RenExSDK) => async (dispatch: Dispatch) => {

    const hiddenOrders = List(await sdk.fetchOrderbook({ limit: 10 }));

    dispatch(setHiddenOrders({ hiddenOrders }));
};
