import * as React from "react";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { Link, RouteComponentProps, RouteProps, withRouter } from "react-router-dom";
import { bindActionCreators, Dispatch } from "redux";

import { setAlert } from "../actions/alert/alertActions";
import { logout } from "../actions/trader/accountActions";
import { _captureInteractionException_ } from "../lib/errors";
import { Alert, AlertType, ApplicationData } from "../reducers/types";
import { Blocky } from "./Blocky";

/**
 * Header is a visual component providing page branding and navigation.
 */
class HeaderClass extends React.Component<Props & RouteProps, State> {
    public constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            copied: false,
            dropdownVisible: false,
        };
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { address, withMenu } = this.props;
        const { dropdownVisible, copied } = this.state;
        const route = this.props.location ? this.props.location.pathname : "";
        const path = route.split("/")[1];

        const loggedIn = (address != null);

        return (
            <div className="header">
                <div className="container">
                    <Link to={loggedIn ? "/exchange" : "/"}>
                        <div className="header--logo" />
                    </Link>
                    {withMenu ?
                        <ul className="header--menu">
                            <li className={path === "exchange" ? "active" : ""}><Link to="/exchange"><span>Exchange</span></Link></li>
                            <li className={path === "orders" ? "active" : ""}><Link to="/orders"><span>Orders</span></Link></li>
                            <li className={path === "balances" ? "active" : ""}><Link to="/balances"><span>Balances</span></Link></li>
                            <li className="header--logout" role="button" onClick={this.handleLogOut}><Link to=""><span>Logout</span></Link></li>
                            {address &&
                                <li
                                    className="header--account"
                                    onMouseEnter={this.showDropdown}
                                    onMouseLeave={this.hideDropdown}
                                >
                                    <Blocky address={address} />
                                    {dropdownVisible ?
                                        <ul className="header--dropdown">
                                            <li role="button" onClick={this.copyToClipboard}>
                                                <span data-addr={address}>
                                                    {copied ?
                                                        <span>Copied</span>
                                                        :
                                                        <span>{address.substring(0, 8)}...{address.slice(-5)}</span>
                                                    }
                                                </span>
                                            </li>
                                            <li role="button" onClick={this.handleLogOut}>Logout</li>
                                        </ul> : null
                                    }
                                </li>
                            }
                        </ul> : null
                    }
                </div>
            </div>
        );
    }

    private readonly showDropdown = (): void => {
        this.setState({ dropdownVisible: true, copied: false });
    }

    private readonly hideDropdown = (): void => {
        this.setState({ dropdownVisible: false, copied: false });
    }

    private readonly copyToClipboard = (e: React.MouseEvent<HTMLElement>): void => {
        const el = e.currentTarget.childNodes[0] as Element;
        const address = el.getAttribute("data-addr");
        if (address) {
            const dummy = document.createElement("input");
            document.body.appendChild(dummy);
            dummy.setAttribute("value", address);
            dummy.select();
            document.execCommand("copy");
            document.body.removeChild(dummy);
        }
        this.setState({ copied: true });
    }

    private readonly handleLogOut = async (): Promise<void> => {
        const { sdk } = this.props;
        try {
            await this.props.actions.logout(sdk, { reload: true });
        } catch (error) {
            const message = "Something went wrong while logging out.";

            this.props.actions.setAlert({
                alert: new Alert({
                    alertType: AlertType.Error,
                    message,
                })
            });

            _captureInteractionException_(error, {
                description: message,
                shownToUser: "As alert",
            });
        }
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    address: state.trader.address,
    sdk: state.sdk.sdk,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        logout,
        setAlert,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps>, RouteComponentProps {
    withMenu: boolean;
}

interface State {
    dropdownVisible: boolean;
    copied: boolean;
}

export const Header = withRouter(connect(mapStateToProps, mapDispatchToProps)(HeaderClass));
