import React from "react";

import BigNumber from "bignumber.js";

import { Field, FieldProps, Form, FormikProps } from "formik";
import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { OrderSettlement, OrderSide } from "@renex/renex";

import { Loading } from "./Loading";

import { setOrder } from "../actions/inputs/inputsActions";
import { removeExcessDigits } from "../lib/balances";
import { getPriceStep, getVolumeStep } from "../lib/conversion";
import { _captureInteractionException_ } from "../lib/errors";
import { Pair, Pairs, TokenDetail, TokenDetails } from "../lib/market";
import { ApplicationData } from "../reducers/types";
import { InfoLabel } from "./InfoLabel";
import { NewOrderInputField } from "./NewOrderInputField";
import { NewOrderInputsValues } from "./NewOrderInputs";

// tslint:disable: jsx-no-lambda
class NewOrderInputFormClass extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.state = {
            priorityTokenDetails: undefined,
            nonPriorityTokenDetails: undefined,
            feeToken: undefined,
            notice: undefined,
        };
    }

    public componentDidMount(): void {
        // Validate fields on load
        const { values, setValues } = this.props;
        const newState = this.calculateNewState(values);
        setValues(newState);

        this.preRender(this.props);
    }

    public componentWillUnmount(): void {
        // Save inputs when the user navigates away from the page
        const { values, side } = this.props;
        this.props.actions.setOrder({
            orderInputs: {
                side,
                pricepoint: values.pricepoint,
                volume: values.volume,
                minVolume: values.minVolume,
            }
        });
    }

    public async componentWillReceiveProps(nextProps: Props): Promise<void> {
        const { market, side, allOrNothing, initialMarketPrice, validateForm, values } = this.props;

        // Update the market price if the market or market price has changed
        const marketChanged = market !== nextProps.market;
        const initialMarketPriceChanged = initialMarketPrice !== nextProps.initialMarketPrice;
        const price = new BigNumber(values.pricepoint || "0");
        const minValueNotSet = !values.minVolume && !nextProps.values.minVolume;
        if (minValueNotSet || marketChanged || initialMarketPriceChanged && price.lte(new BigNumber(0))) {
            nextProps.values.pricepoint = nextProps.initialMarketPrice;
            const pairDetails = Pairs.get(nextProps.market, undefined);
            const pricepointBigNumber = new BigNumber(nextProps.initialMarketPrice);
            if (pairDetails && !pricepointBigNumber.isZero()) {
                const absoluteMinVolume = nextProps.calculateAbsoluteMinTradeVolume(pairDetails.baseToken, pricepointBigNumber);
                nextProps.values.minVolume = new BigNumber(absoluteMinVolume).toFixed();
            }
        }

        // Match minVolume to volume when All or Nothing is enabled
        const allOrNothingEnabled = !allOrNothing && nextProps.allOrNothing;
        if (allOrNothingEnabled) {
            nextProps.values.minVolume = nextProps.values.volume;
        }

        // Re-validate form
        const sideChanged = side !== nextProps.side;
        if (sideChanged || marketChanged || allOrNothing !== nextProps.allOrNothing) {
            await validateForm();
        }

        this.preRender(nextProps);
    }

    // The code that used to be at the start of render has been moved here
    public preRender = (props: Props) => {
        const { values, settlementType, market, side } = props;

        const marketDetails = Pairs.get(market, undefined);
        if (!marketDetails) { return; }

        const priorityTokenDetails = TokenDetails.get(marketDetails.quoteToken, undefined);
        const nonPriorityTokenDetails = TokenDetails.get(marketDetails.baseToken, undefined);

        if (!priorityTokenDetails || !nonPriorityTokenDetails) { return; }

        let feeToken;
        let notice: string;
        if (settlementType === OrderSettlement.RenExAtomic) {
            feeToken = side === OrderSide.BUY ? priorityTokenDetails : nonPriorityTokenDetails;
            const sellAmount = new BigNumber(side === OrderSide.BUY ? values.priorityVolume : values.volume);
            notice = sellAmount.gt(new BigNumber(0)) ? `You will spend ${sellAmount.plus(this.feeAmount()).toFixed()} ${feeToken.symbol} after fees.` : "";
        } else {
            feeToken = side === OrderSide.BUY ? nonPriorityTokenDetails : priorityTokenDetails;
            const buyAmount = new BigNumber(side === OrderSide.BUY ? values.volume : values.priorityVolume);
            notice = buyAmount.gt(new BigNumber(0)) ? `You will receive ${buyAmount.minus(this.feeAmount()).toFixed()} ${feeToken.symbol} after fees.` : "";
        }

        this.setState({
            priorityTokenDetails,
            nonPriorityTokenDetails,
            notice,
            feeToken,
        });
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { side, feePercent, isSubmitting, isValid } = this.props;
        const { feeToken, nonPriorityTokenDetails } = this.state;

        if (!feeToken || !nonPriorityTokenDetails) {
            return <></>;
        }

        return (
            <Form>
                <div className="order--inputs">
                    <Field
                        name="pricepoint"
                        render={this.pricepointRender}
                    />
                    <Field
                        name="volume"
                        render={this.volumeRender}
                    />
                    <Field
                        name="minVolume"
                        render={this.renderMinVolume}
                    />
                    <Field
                        name="priorityVolume"
                        render={this.renderPriorityVolume}
                    />
                </div>
                <div>
                    <div>
                        <span>Fee: {this.feeAmount().toFixed()} {feeToken.symbol}</span>
                        <InfoLabel>Fees are set to {feePercent.times(100).toFixed()}% of the order volume.</InfoLabel>
                    </div>
                </div>
                <div className="order--footer">
                    <button className={`order--button ${isSubmitting ? "submitting" : ""}`} type="submit" disabled={!isValid || isSubmitting}>
                        <span>
                            {
                                isSubmitting ? <Loading /> :
                                    <>{side === OrderSide.BUY ? "Buy" : "Sell"} {nonPriorityTokenDetails.symbol}</>
                            }
                        </span>
                    </button>
                </div>
            </Form>
        );
    }

    private readonly pricepointRender = (field: FieldProps) => {
        const { market, initialMarketPrice } = this.props;

        const marketDetails = Pairs.get(market, undefined);
        if (!marketDetails) { return <></>; }

        const priorityTokenDetails = TokenDetails.get(marketDetails.quoteToken, undefined);
        if (!priorityTokenDetails) { return <></>; }

        const prioritySymbol = priorityTokenDetails.symbol;

        return <NewOrderInputField
            {...field}
            title={<>
                Price (<span
                    role="button"
                    className="order--option"
                    // tslint:disable-next-line:react-this-binding-issue
                    onClick={() => {
                        this.setAndUpdateValues(field.field.name, initialMarketPrice);
                    }}
                >
                    Market
                </span>)
            </>}
            disabled={marketDetails.fixedPrice !== undefined}
            ticker={prioritySymbol}
            stepFunction={getPriceStep}
            handleChange={this.handleChange}
            handleBlur={this.handleBlur}
        />;
    }

    private readonly volumeRender = (field: FieldProps) => {
        const { side, settlementType } = this.props;
        const { nonPriorityTokenDetails, notice } = this.state;

        if (!nonPriorityTokenDetails) {
            return <></>;
        }

        return <NewOrderInputField
            {...field}
            title={<>
                {side === OrderSide.BUY ? "Receive" : "Spend"} (<span role="button" className="order--option" onClick={this.fillVolume}>Max</span>)
            </>}
            ticker={nonPriorityTokenDetails.symbol}
            stepFunction={getVolumeStep}
            handleChange={this.handleChange}
            handleBlur={this.handleBlur}
            notice={settlementType === OrderSettlement.RenExAtomic ? (side === OrderSide.SELL && notice) : (side === OrderSide.BUY && notice)}
        />;
    }

    private readonly renderMinVolume = (field: FieldProps) => {
        const { allOrNothing } = this.props;
        const { nonPriorityTokenDetails } = this.state;

        if (!nonPriorityTokenDetails) {
            return <></>;
        }

        return <NewOrderInputField
            {...field}
            title="Minimum Fill Amount"
            ticker={nonPriorityTokenDetails.symbol}
            hint="The minimum volume you are willing to be matched with. This does not affect price but higher minimum volumes reduce the chances of order match."
            disabled={allOrNothing}
            stepFunction={getVolumeStep}
            handleChange={this.handleChange}
            handleBlur={this.handleBlur}
        />;
    }

    private readonly renderPriorityVolume = (field: FieldProps) => {
        const { side, settlementType } = this.props;
        const { priorityTokenDetails, notice } = this.state;

        if (!priorityTokenDetails) {
            return <></>;
        }

        return <NewOrderInputField
            {...field}
            title={side === OrderSide.BUY ? "Spend" : "Receive"}
            ticker={priorityTokenDetails.symbol}
            disabled={true}
            stepFunction={getVolumeStep}
            notice={settlementType === OrderSettlement.RenExAtomic ? (side === OrderSide.BUY && notice) : (side === OrderSide.SELL && notice)}
        />;
    }

    private calculateNewState(oldState: NewOrderInputsValues, name?: string, valueIn?: string): NewOrderInputsValues {
        let newState = { ...oldState };

        if (name) {
            // If value is undefined, set it to previous state
            let value = (valueIn === undefined) ? oldState[name] : valueIn;

            // If the value is in scientific notation, fix it
            if (value.toLowerCase().indexOf("e") !== -1) {
                value = new BigNumber(value).toFixed();
            }

            newState = { ...newState, [name]: value };
        }
        const { pricepoint, volume } = newState;

        // Match minVolume to volume if we're an All Or Nothing trade
        if (this.props.allOrNothing) {
            newState.minVolume = newState.volume;
        }

        // Calculate the new priority volume
        const price = new BigNumber(pricepoint || "0");
        const nonPriorityVolume = new BigNumber(volume || "0");
        const priorityVolume = nonPriorityVolume.times(price);
        newState.priorityVolume = priorityVolume.toFixed();

        return newState;
    }

    private coerceValues(state: NewOrderInputsValues, name: string): NewOrderInputsValues {
        const { sdk, market, initialMarketPrice, calculateAbsoluteMinTradeVolume, side } = this.props;
        const { normalizePrice, normalizeVolume } = sdk.utils;

        const pairDetails = Pairs.get(market, undefined);
        if (!pairDetails) {
            return state;
        }

        const pricepointBigNumber = new BigNumber(normalizePrice(new BigNumber(state.pricepoint || "0"), side === OrderSide.SELL));
        const volumeBigNumber = new BigNumber(normalizeVolume(removeExcessDigits(new BigNumber(state.volume || "0"), pairDetails.baseToken)));
        const minVolumeBigNumber = new BigNumber(normalizeVolume(removeExcessDigits(new BigNumber(state.minVolume || "0"), pairDetails.baseToken)));
        const absoluteMinVolume = calculateAbsoluteMinTradeVolume(pairDetails.baseToken, pricepointBigNumber);

        const newState = { ...state };
        switch (name) {
            case "pricepoint":
                // Reset price to default if price is empty or zero
                if (pricepointBigNumber.lte(new BigNumber(0))) {
                    newState.pricepoint = initialMarketPrice;
                } else {
                    // Set pricepoint to normalized price
                    newState.pricepoint = pricepointBigNumber.toFixed();
                }
                break;
            case "volume":
                // Set volume to normalized volume
                newState.volume = volumeBigNumber.toFixed();
                // Coerce minimum volume if volume < min volume
                if (volumeBigNumber.lt(minVolumeBigNumber)) {
                    newState.minVolume = BigNumber
                        .max(volumeBigNumber, absoluteMinVolume)
                        .toFixed();
                }
                break;
            case "minVolume":
                // Set min volume to normalized volume
                newState.minVolume = minVolumeBigNumber.toFixed();

                // Enforce absolute minimum volume
                if (minVolumeBigNumber.lt(absoluteMinVolume)) {
                    newState.minVolume = absoluteMinVolume.toFixed();
                }

                // Coerce volume if min volume > volume
                if (minVolumeBigNumber.gt(volumeBigNumber)) {
                    newState.volume = newState.minVolume;
                }
            default:
                const description = `Unexpected value name ${name} in coerceValues`;
                _captureInteractionException_(
                    new Error(description),
                    { description, shownToUser: "No" },
                );
        }
        return this.calculateNewState(newState, name);
    }

    private readonly feeAmount = (): BigNumber => {
        const { values, side, feePercent, settlementType } = this.props;
        const nonPriorityVolume = new BigNumber(values.volume || "0");
        const priorityVolume = new BigNumber(values.priorityVolume || "0");
        let feeAmount;
        if (settlementType === OrderSettlement.RenExAtomic) {
            feeAmount = side === OrderSide.BUY ? priorityVolume.times(feePercent) : nonPriorityVolume.times(feePercent);
        } else {
            feeAmount = side === OrderSide.BUY ? nonPriorityVolume.times(feePercent) : priorityVolume.times(feePercent);
        }
        return feeAmount;
    }

    private readonly fillVolume = (): void => {
        const { market, values, side, settlementType } = this.props;
        const usableLegacyBalances = settlementType === OrderSettlement.RenEx ? this.props.usableLegacyBalances : this.props.usableAtomicBalances;
        const { pricepoint } = values;
        const marketDetails = Pairs.get(market, undefined);
        if (!marketDetails) {
            return;
        }

        const priorityToken = marketDetails.quoteToken;
        const nonPriorityToken = marketDetails.baseToken;

        let nonPriorityVolume = new BigNumber(0);
        if (side === OrderSide.BUY) {
            const price = new BigNumber(pricepoint || "0");
            if (!price.isZero()) {
                const priorityVolume = usableLegacyBalances.get(priorityToken) || new BigNumber(0);
                nonPriorityVolume = priorityVolume.div(price);
            }
        } else {
            nonPriorityVolume = usableLegacyBalances.get(nonPriorityToken) || new BigNumber(0);
        }
        this.setAndUpdateValues("volume", nonPriorityVolume.toFixed(), true);
    }

    private readonly handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
        const { name, value } = e.target;
        this.setAndUpdateValues(name, value);
    }

    private readonly handleBlur = (e: React.ChangeEvent<HTMLInputElement>): void => {
        const { name, value } = e.target;
        this.setAndUpdateValues(name, value, true);
    }

    private setAndUpdateValues(name: string, value: string, blur?: boolean): void {
        const { setValues, values } = this.props;
        let newState = this.calculateNewState(values, name, value);
        // Coerce values if necessary
        if (blur) {
            newState = this.coerceValues(newState, name);
        }
        setValues(newState);
    }

}

const mapStateToProps = (state: ApplicationData) => ({
    sdk: state.sdk.sdk,
    feePercent: state.sdk.darknodeFeePercent,
    usableLegacyBalances: state.trader.usableLegacyBalances,
    usableAtomicBalances: state.trader.usableAtomicBalances,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        setOrder,
    }, dispatch)
});

interface FormProps extends FormikProps<NewOrderInputsValues> {
    market: Pair;
    side: OrderSide;
    allOrNothing: boolean;
    initialMarketPrice: string;
    settlementType: OrderSettlement;
    calculateAbsoluteMinTradeVolume(baseToken: string, price: BigNumber): BigNumber;
}

interface Props extends FormProps, ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
}

interface State {
    priorityTokenDetails: TokenDetail | undefined;
    nonPriorityTokenDetails: TokenDetail | undefined;
    feeToken: TokenDetail | undefined;
    notice: string | undefined;
}

export const NewOrderInputForm = connect(mapStateToProps, mapDispatchToProps)(NewOrderInputFormClass);
