import * as Sentry from "@sentry/browser";
import * as React from "react";

import Web3 from "web3";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";
import { Provider } from "web3/providers";

import { setAlert } from "../actions/alert/alertActions";
import { clearPopup, setPopup } from "../actions/popup/popupActions";
import { login } from "../actions/trader/accountActions";
import { storeWallet } from "../actions/trader/walletActions";
import { _captureInteractionException_ } from "../lib/errors";
import { networkData } from "../lib/network";
import { ErrorCanceledByUser, ErrorVerificationFailed, Wallet, WalletDetails } from "../lib/wallets/wallet";
import { Alert, AlertType, ApplicationData } from "../reducers/types";
import { getParameterByName } from "./pages/Home";
import { SelectWeb3Account } from "./popups/SelectWeb3Account";
import { VerificationPopup } from "./popups/VerificationPopup";

/**
 * WalletItem is a visual component representing an individual wallet selection
 */
class WalletItemClass extends React.Component<Props, State> {
    public constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            referrer: "",
            canceled: false
        };
    }

    public componentDidMount(): void {
        if (window.location.href.indexOf("?ref=") !== -1) {
            const referrer = getParameterByName("ref");
            if (referrer != null && referrer !== "" && this.state.referrer === "") {
                this.setState({ referrer });
            }
        }
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { wallet } = this.props;
        const walletDetails = WalletDetails.get(wallet, undefined);

        if (walletDetails === undefined) {
            return <></>;
        }

        const { slug, enabled, description, name } = walletDetails;

        return (
            <div className={`wallets--container ${slug} ${enabled ? "" : "disabled"}`}>
                <div
                    className="wallets--item"
                    role="button"
                    onClick={this.handleSelectWallet}
                >
                    <span className="wallets--title">{name}</span>
                    <p className="wallets--description">
                        {!enabled ?
                            "Coming soon" : null
                        }
                        {enabled && description !== "" ?
                            description : null
                        }
                    </p>
                </div>
            </div>
        );
    }

    private readonly handleSelectWallet = async (): Promise<void> => {
        const { sdk, wallet } = this.props;
        const { referrer } = this.state;

        const walletDetails = WalletDetails.get(wallet, undefined);
        if (!walletDetails) {
            return;
        }

        Sentry.configureScope((scope) => {
            scope.setExtra("wallet", walletDetails.slug);
        });

        const { getWeb3Provider } = walletDetails;

        this.setState({ canceled: false });
        this.props.actions.storeWallet({ wallet });

        // Retrieve web3.
        let provider: Provider;
        let providerAccounts: string[] | null;
        try {
            [provider, providerAccounts] = await getWeb3Provider();
        } catch (error) {
            if (error.message !== ErrorCanceledByUser) {

                _captureInteractionException_(error, {
                    description: "Error in WalletItem.handleSelectWallet",
                    shownToUser: "As alert",
                });
                this.props.actions.setAlert({
                    alert: new Alert({ message: error.message })
                });
            }
            this.props.actions.storeWallet({ wallet: null });

            return;
        }

        const resolve = async (address: string, url: string | null) => {
            // tslint:disable-next-line: await-promise
            await this.props.actions.login(sdk, provider, address, { redirect: true, url });
            this.props.actions.clearPopup();
            this.props.actions.setAlert({
                alert: new Alert({
                    message: "You have been successfully logged in",
                    alertType: AlertType.Success
                })
            });
        };

        const cancel = () => {
            this.props.actions.clearPopup();

            // Set the state to canceled, so any pending verification is
            // halted.
            this.setState({ canceled: true });
        };

        const reject = (address: string, reason: string) => {
            if (reason === ErrorVerificationFailed) {
                if (this.state.canceled) {
                    return;
                }
                this.props.actions.setPopup({ popup: <VerificationPopup address={address} wallet={wallet} closePopup={cancel} />, onCancel: cancel });
            } else {
                cancel();
            }
        };

        const getAccounts = async () => {
            const web3 = new Web3(provider);

            let accounts;
            try {
                accounts = providerAccounts ? providerAccounts : await web3.eth.getAccounts();
            } catch (error) {
                if (wallet === Wallet.Ledger && error.message.match(/invalid transport instance/)) {
                    throw new Error("Unable to connect to Ledger.");
                } else {
                    _captureInteractionException_(error, {
                        description: "Error in WalletItem.getAccounts",
                        shownToUser: "In message box in SelectWeb3Popup",
                    });
                    throw error;
                }
            }

            if (accounts.length === 0) {
                if (wallet === Wallet.MetaMask) {
                    throw new Error("Please unlock your Metamask.");
                } else {
                    throw new Error("No accounts found. Your wallet may be locked.");
                }
            }

            // Check that wallet is on correct network (except for Ledger and
            // Keystore, for which we have already set the network)
            if (wallet !== Wallet.Ledger && wallet !== Wallet.Keystore) {
                // tslint:disable-next-line:no-any
                const network = await (web3.eth.net as any).getNetworkType();

                if (network !== networkData.ethNetwork) {
                    throw new Error(`Please ensure you are on the ${networkData.ethNetworkLabel} network`);
                }
            }

            return accounts;
        };

        const popup = {
            popup: <SelectWeb3Account referrer={referrer} getAccounts={getAccounts} resolve={resolve} reject={reject} message={"Select account"} />,
            onCancel: cancel,
        };

        this.props.actions.setPopup(popup);
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    sdk: state.sdk.sdk,
    url: state.trader.url,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        setAlert,
        storeWallet,
        setPopup,
        clearPopup,
        login,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
    wallet: Wallet;
}

interface State {
    referrer: string;
    canceled: boolean;
}

export const WalletItem = connect(mapStateToProps, mapDispatchToProps)(WalletItemClass);
