import * as React from "react";

import { connect } from "react-redux"; // Custom typings

import { ApplicationData, PopupDetails } from "../reducers/types";
import { _catch_ } from "./ErrorBoundary";

/**
 * Popup is a visual component for displaying an arbitrary component in the
 * foreground with the rest of the page in the background
 */
class PopupClass extends React.Component<Props> {
    constructor(props: Props) {
        super(props);
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {

        if (this.props.popups.size === 0) {
            return <></>;
        }

        return this.props.popups.map((popupDetails: PopupDetails, index: number) => {
            const { popup, onCancel } = popupDetails;

            console.log(popup);

            return (
                <div key={index} className="popup--outer">
                    <div role="none" className="overlay" onClick={this.onClickHandler} />
                    {_catch_(popup, { popup: true, onCancel })}
                </div>
            );
        });
    }

    public onClickHandler = () => {
        const { dismissible, onCancel } = this.props.popups.last();
        if (dismissible) {
            onCancel();
        }
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    popups: state.popup.stack,
});

interface Props extends ReturnType<typeof mapStateToProps> {
}

export const Popup = connect(mapStateToProps)(PopupClass);
