import { combineReducers } from "redux";

import { alertReducer } from "./alert/alertReducer";
import { balancesReducer } from "./balances/balancesReducer";
import { inputsReducer } from "./inputs/inputsReducer";
import { marketReducer } from "./market/marketReducer";
import { hiddenBookReducer } from "./orderbook/hiddenBookReducer";
import { traderBookReducer } from "./orderbook/traderBookReducer";
import { popupReducer } from "./popup/popupReducer";
import { sdkReducer } from "./sdk/sdkReducer";
import { traderReducer } from "./trader/traderReducer";

import { ApplicationData } from "./types";

export const rootReducer = combineReducers<ApplicationData>({
    alert: alertReducer,
    balanceHistory: balancesReducer,
    hiddenBook: hiddenBookReducer,
    inputs: inputsReducer,
    marketPrices: marketReducer,
    popup: popupReducer,
    traderBook: traderBookReducer,
    trader: traderReducer,
    sdk: sdkReducer,
});
