import * as React from "react";

import { AtomicConnectionStatus, TokenCode } from "@renex/renex";
import { connect } from "react-redux"; // Custom typings

import { RenExAtomicTokens, TokenDetails, UnknownToken } from "../lib/market";
import { ApplicationData, LabelLevel } from "../reducers/types";
import { AtomicNotice } from "./AtomicNotice";
import { Heading } from "./Heading";
import { InfoLabel } from "./InfoLabel";

/**
 * ViewAtomicBalances is a visual component for displaying the user's balances
 * held by the atomic-swap software, Atom
 */
class ViewAtomicBalancesClass extends React.Component<Props, State> {
    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        return (
            <div className="section view">
                <div className="container">
                    <Heading title="Balances held by Swapperd" />
                    {!this.atomConnected() ? <AtomicNotice /> :
                        <div className="view--table table--wrapper">
                            <table>
                                <thead>
                                    <tr>
                                        <th>Token</th>
                                        <th>Address</th>
                                        <th>Total Balance
                                </th>
                                        <th>
                                            Usable Balance
                                    <InfoLabel>The amount you have deposited, excluding balances locked in open orders.</InfoLabel>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.generateRows()}
                                </tbody>
                            </table>
                        </div>
                    }
                </div>
            </div>
        );
    }

    private atomConnected(): boolean {
        const { atomConnectionStatus } = this.props;
        return (
            atomConnectionStatus === AtomicConnectionStatus.ConnectedLocked ||
            atomConnectionStatus === AtomicConnectionStatus.ConnectedUnlocked
        );
    }

    private generateRows(): React.ReactNode[] {
        const list: React.ReactNode[] = [];
        const { atomicAddresses, atomicBalances, usableAtomicBalances, supportedAtomicTokens } = this.props;
        RenExAtomicTokens(supportedAtomicTokens)
            .forEach((token: TokenCode) => {
                const { name, symbol, icon } = TokenDetails.get(token, UnknownToken);

                const address = atomicAddresses.get(token, undefined) || "-";

                const balance = atomicBalances.get(token, undefined);
                const balanceReadable = balance !== undefined && balance !== null ? `${balance.toFixed()} ${symbol}` : "-";

                const usable = usableAtomicBalances.get(token, undefined);
                const usableReadable = usable !== undefined && usable !== null ? `${usable.toFixed()} ${symbol}` : "-";

                // let locked = balance.minus(usable);
                // if (locked.isLessThan(0)) {
                //     locked = new BigNumber(0);
                // }

                // tslint:disable-next-line:non-literal-require
                const image = require(`../tokens/${icon}`);

                list.push(
                    <tr key={token}>
                        <td>
                            <img alt="" role="presentation" src={image} />
                            <span>{`${name} `}</span>
                        </td>
                        <td className="monospace">{address}</td>
                        <td>
                            {balanceReadable}
                            {balance === null &&
                                <InfoLabel level={LabelLevel.Warning}>Failed to retrieve total {symbol} balance.</InfoLabel>
                            }
                        </td>
                        <td>
                            {usableReadable}
                            {usable === null &&
                                <InfoLabel level={LabelLevel.Warning}>Failed to retrieve usable {symbol} balance.</InfoLabel>
                            }
                        </td>
                    </tr >
                );
            });
        return list;
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    atomicAddresses: state.trader.atomicAddresses,
    atomicBalances: state.trader.atomicBalances,
    usableAtomicBalances: state.trader.usableAtomicBalances,
    atomConnectionStatus: state.trader.atomConnectionStatus,
    supportedAtomicTokens: state.sdk.supportedAtomicTokens,
});

interface Props extends ReturnType<typeof mapStateToProps> {
}

interface State {
}

export const ViewAtomicBalances = connect(mapStateToProps)(ViewAtomicBalancesClass);
