import * as Sentry from "@sentry/browser";
import * as React from "react";
import * as ReactDOM from "react-dom";

import RenExSDK, { NetworkData } from "@renex/renex";
import Web3 from "web3";

import { Provider } from "react-redux";
import { PersistGate } from "redux-persist/integration/react";
import { HttpProvider } from "web3/providers";

import { App } from "./components/App";
import { _catch_ } from "./components/ErrorBoundary";
import { configureStore } from "./store/configureStore";

export const { store, persistor } = configureStore();

import "./index.scss";

interface EthereumProvider extends HttpProvider {
    enable(): Promise<void>;
}

declare global {
    interface Window {
        web3: Web3 | undefined;
        ethereum: EthereumProvider | undefined;
        API: string;
        NETWORK: NetworkData;
        INFURA_KEY: string;
        WYRE_KEY: string;
        KYBER_KEY: string;
        SENTRY_DSN: string;
        sdk: RenExSDK;
        RENEX_VERSION: string;
    }
}

// Initialize Sentry error logging
Sentry.init({
    dsn: window.SENTRY_DSN,
    release: window.RENEX_VERSION,
    environment: (process.env.NODE_ENV === "development") ? "local" : window.NETWORK.network,
});

Sentry.configureScope((scope) => {
    scope.setExtra("loggedIn", false);
});

ReactDOM.render(
    _catch_(<Provider store={store}>
        <PersistGate loading={null} persistor={persistor}>
            <App />
        </PersistGate>
    </Provider>),
    document.getElementById("root") as HTMLElement
);
