import BigNumber from "bignumber.js";

import * as React from "react";

import { OrderedMap } from "immutable";
import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { OrderSettlement, TokenCode } from "@renex/renex";

import { Pairs, RenExAtomicTokens, RenExTokens, TokenDetails, UnknownToken } from "../lib/market";
import { ApplicationData } from "../reducers/types";

/**
 * BalanceBanner is a visual component for displaying user balances in a banner
 * format
 */
class BalanceBannerClass extends React.Component<Props, State> {

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { sdk, market, usableLegacyBalances, usableAtomicBalances, supportedTokens, supportedAtomicTokens } = this.props;
        const pair = Pairs.get(market, undefined);
        const atomic = pair !== undefined && pair.orderSettlement === OrderSettlement.RenExAtomic;
        const tokens = RenExTokens(supportedTokens);
        const atomicTokens = RenExAtomicTokens(supportedAtomicTokens);

        return (
            <div className="banner">
                <div className="container">
                    <div className="banner--details">
                        {atomic && sdk.atom.isConnected() ?
                            <>
                                <span>Swapperd balances:</span>
                                <ul>
                                    {this.listBalances(atomicTokens.toArray(), usableAtomicBalances)}
                                </ul>
                            </>
                            :
                            <>
                                <span>Legacy balances:</span>
                                <ul>
                                    {this.listBalances(tokens.toArray(), usableLegacyBalances)}
                                </ul>
                            </>
                        }
                    </div>
                </div>
            </div>
        );
    }

    /**
     * listBalances assembles the list of token balances
     */
    private listBalances(tokens: TokenCode[], balances: OrderedMap<TokenCode, BigNumber | null>): React.ReactNode[] {
        const list: React.ReactNode[] = [];
        for (const token of tokens) {
            const balance = balances.get(token, null);
            const balanceText = (balance !== null) ? balance.toFixed() : "-";

            const { symbol, icon } = TokenDetails.get(token, UnknownToken);

            // tslint:disable-next-line:non-literal-require
            const image = require(`../tokens/${icon}`);

            list.push(
                <li key={token}>
                    {/* tslint:disable-next-line: react-a11y-anchors */}
                    <a><img alt="" role="presentation" src={image} /> {balanceText} {symbol}</a>
                </li>
            );
        }

        return list;
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    sdk: state.sdk.sdk,
    supportedTokens: state.sdk.supportedTokens,
    supportedAtomicTokens: state.sdk.supportedAtomicTokens,
    market: state.inputs.market,
    usableLegacyBalances: state.trader.usableLegacyBalances,
    usableAtomicBalances: state.trader.usableAtomicBalances,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
}

interface State {
}

export const BalanceBanner = connect(mapStateToProps, mapDispatchToProps)(BalanceBannerClass);
