
import RenExSDK, { MarketDetails, TokenCode } from "@renex/renex";
import BigNumber from "bignumber.js";

import { List } from "immutable";

import { createStandardAction } from "typesafe-actions";

export const storeSDK = createStandardAction("STORE_SDK")<{ sdk: RenExSDK }>();

interface StoreSDKDataPayload {
    supportedTokens: List<TokenCode>;
    supportedAtomicTokens: List<TokenCode>;
    supportedMarkets: List<MarketDetails>;
    minEthTradeVolume: BigNumber;
    darknodeFeePercent: BigNumber;
    wrappingFeePercent: BigNumber;
}

export const storeSDKData = createStandardAction("STORE_SDK_DATA")<StoreSDKDataPayload>();
