import Web3 from "web3";

export const getAccounts = async (web3: Web3): Promise<string[]> => {
    return web3.eth.getAccounts();
};

export const getNetwork = async (web3: Web3): Promise<string> => {
    // tslint:disable-next-line:no-any
    return (web3.eth.net as any).getNetworkType();
};

export const includesAddress = async (web3: Web3, addressIn: string): Promise<boolean> => {
    const address = addressIn.toLowerCase();
    return getAccounts(web3)
        .then((accounts: string[]) => {
            return accounts.map(acc => acc.toLowerCase())
                .includes(address);
        });
};
