import * as React from "react";

import { connect } from "react-redux"; // Custom typings

import { Order, OrderStatus } from "@renex/renex";

import { Heading } from "./Heading";
import { Loading } from "./Loading";

import { ApplicationData } from "../reducers/types";

const NUMBER_OF_ORDERS = 10;

const HiddenOrderStatusDisplay = {
    [OrderStatus.NOT_SUBMITTED]: "-",
    [OrderStatus.OPEN]: "Open",
    [OrderStatus.CONFIRMED]: "Matched",
    [OrderStatus.CANCELED]: "Canceled",
    [OrderStatus.SETTLED]: "Settled",
};
/**
 * HiddenOrderbook is a visual component for displaying a list of orders from
 * the Republic Protocol darkpool
 */
class HiddenOrderbookClass extends React.Component<Props> {
    public constructor(props: Props, context: object) {
        super(props, context);
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { hiddenOrders } = this.props;
        return (
            <div className="section orderbook">
                <div className="container">
                    <Heading title="Hidden orderbook" description={`Last ${NUMBER_OF_ORDERS} orders`} />
                    {hiddenOrders ?
                        <div className="table--wrapper">
                            <table>
                                <thead>
                                    <tr>
                                        <th>Order ID</th>
                                        <th className="center">Status</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {hiddenOrders.size > 0 ?
                                        hiddenOrders
                                            .reverse()
                                            .toArray()
                                            .map((order: Order) =>
                                                <tr key={order.id}>

                                                    <td className="truncate"><span className="monospace">{order.id}</span></td>
                                                    <td className="center">{HiddenOrderStatusDisplay[order.status]}</td>
                                                </tr>
                                            ) :
                                        <tr />
                                    }
                                </tbody>
                            </table>
                        </div>
                        :
                        <Loading />
                    }
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    hiddenOrders: state.hiddenBook.hiddenOrders,
    sdk: state.sdk.sdk,
});

interface Props extends ReturnType<typeof mapStateToProps> {
}

export const HiddenOrderbook = connect(mapStateToProps)(HiddenOrderbookClass);
