import * as React from "react";

import Axios from "axios";

import { connect } from "react-redux"; // Custom typings

import { Blocky } from "../../components/Blocky";
import { Loading } from "../../components/Loading";
import { _captureInteractionException_ } from "../../lib/errors";
import { ErrorVerificationFailed } from "../../lib/wallets/wallet";
import { ApplicationData } from "../../reducers/types";

/**
 * SelectWeb3Account is a popup component for prompting a user to select an
 * Ethereum account from multiple addresses
 */
class SelectWeb3AccountClass extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            accounts: null,
            error: null,
        };
    }

    public async componentDidMount() {
        await this.getAccounts();
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { accounts, error } = this.state;

        if (error) {
            return (
                <div className="popup ledger">
                    <h2>Unable to retrieve accounts</h2>
                    <p>{error}</p>
                    <button onClick={this.getAccounts}>Retry</button>
                </div>
            );
        }

        return (
            <div className="popup accounts">
                {accounts !== null ?
                    <>
                        <h2>Select an account:</h2>
                        {accounts.map((account, i) =>
                            <button key={i} data-item={i} className="account--button" onClick={this.onSelectAccount}>
                                <div className="account--left">
                                    <Blocky address={account} />
                                </div>
                                <div className="account--right">
                                    <span className="monospace">{account}</span>
                                </div>
                            </button>
                        )}
                    </>
                    :
                    <Loading />
                }
            </div>
        );
    }

    private readonly getAccounts = async () => {
        const { referrer } = this.props;
        this.setState({ error: null });
        let accounts;
        try {
            accounts = await this.props.getAccounts();
        } catch (error) {
            // Errors already logged to Sentry
            this.setState({ error: (error as Error).message || error.toString() });
            return;
        }
        if (accounts.length === 1) {
            await this.attemptVerification(accounts[0], referrer);
            return;
        }
        this.setState({ accounts });
    }

    private readonly onSelectAccount = async (event: React.MouseEvent<HTMLButtonElement>): Promise<void> => {
        const { referrer } = this.props;
        const { accounts } = this.state;
        const selectedAccountIndex = parseInt(event.currentTarget.getAttribute("data-item") || "0", 10);

        if (selectedAccountIndex === null || accounts === null || accounts.length < selectedAccountIndex) {
            console.error("No account selected");
            return;
        }
        const address = accounts[selectedAccountIndex];
        await this.attemptVerification(address, referrer);
    }

    private readonly attemptVerification = async (address: string, referrer: string): Promise<void> => {
        const { url } = this.props;
        const data = { address, referrer };

        try {
            // This request stores the trader login details (address and
            // referrer) in the Ingress database and performs a check to find
            // whether the trader is verified.
            const response = await Axios.post(`${window.NETWORK.ingress}/login`, JSON.stringify(data));
            if (response.data.verified) {
                this.props.resolve(address, url);
                return;
            }
        } catch (error) {
            _captureInteractionException_(error, {
                description: "Error in SelectWeb3Account.attemptVerification",
                shownToUser: "No",
            });
        }

        this.props.reject(address, ErrorVerificationFailed);
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    sdk: state.sdk.sdk,
    url: state.trader.url,
});

interface Props extends ReturnType<typeof mapStateToProps> {
    message: string;
    referrer: string;
    getAccounts(): Promise<string[]>;
    resolve(address: string, url: string | null): void;
    reject(address: string, reason: string): void;
}

interface State {
    accounts: string[] | null;
    error: string | null;
}

export const SelectWeb3Account = connect(mapStateToProps)(SelectWeb3AccountClass);
