import { ActionType, getType } from "typesafe-actions";

import * as popupActions from "../../actions/popup/popupActions";

import { PopupData, PopupDetails } from "../../reducers/types";

type PopupAction = ActionType<typeof popupActions>;

export const popupReducer = (state: PopupData = new PopupData(), action: PopupAction) => {
    switch (action.type) {
        case getType(popupActions.setPopup):
            document.documentElement.classList.add("noscroll");

            const stackItem: PopupDetails = {
                popup: action.payload.popup,
                dismissible: action.payload.dismissible !== false,
                onCancel: action.payload.onCancel,
            };

            return state.set("stack", state.stack.push(stackItem));
        case getType(popupActions.clearPopup):
            document.documentElement.classList.remove("noscroll");
            return state.set("stack", state.stack.pop());
        default:
            return state;
    }
};
