import * as React from "react";

import Web3 from "web3";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { Redirect, Route, RouteComponentProps, Router, withRouter } from "react-router-dom";
import { bindActionCreators, Dispatch } from "redux";

import { setAlert } from "../actions/alert/alertActions";
import { clearPopup, setPopup } from "../actions/popup/popupActions";
import { login, logout, storeURL } from "../actions/trader/accountActions";
import { _captureBackgroundException_, _captureInteractionException_ } from "../lib/errors";
import { history } from "../lib/history";
import { getInjectedWeb3Provider } from "../lib/wallets/web3browser";
import { includesAddress } from "../lib/web3";
import { Alert, AlertType, ApplicationData } from "../reducers/types";
import { Alerts } from "./Alerts";
import { BackgroundTasks } from "./BackgroundTasks";
import { _catch_ } from "./ErrorBoundary";
import { Balances } from "./pages/Balances";
import { Exchange } from "./pages/Exchange";
import { Home } from "./pages/Home";
import { LoggingOut } from "./pages/LoggingOut";
import { Orders } from "./pages/Orders";
import { Popup } from "./Popup";
import { newMigrationPopup } from "./popups/MigrationPopup";
import { newTermsPopup } from "./popups/TermsPopup";

// Scroll restoration based on https://reacttraining.com/react-router/web/guides/scroll-restoration
const ScrollToTop = withRouter(
    // tslint:disable-next-line:no-any
    class ScrollToTopWithoutRouter extends React.Component<RouteComponentProps<any>> {
        // tslint:disable-next-line:no-any
        public componentDidUpdate(prevProps: Readonly<RouteComponentProps<any>>): void {
            if (this.props.location !== prevProps.location) {
                window.scrollTo(0, 0);
            }
        }

        /**
         * The main render function.
         * @dev Should have minimal computation, loops and anonymous functions.
         */
        public render(): React.ReactNode {
            return <></>;
        }
    }
);

/**
 * App is the main visual component responsible for displaying different routes
 * and running background app loops
 */
class AppClass extends React.Component<Props, State> {

    public constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            checkingReLogin: false,
        };
    }

    public async componentDidMount(): Promise<void> {
        const { agreedToTerms, sdk, seenMigration } = this.props;

        this.setState({ checkingReLogin: true });

        // Check if user was logged-in already
        const loggedIn = await this.handleReLogin();
        if (!loggedIn) {
            await this.props.actions.logout(sdk, { reload: false });
        }
        this.setState({ checkingReLogin: false });

        const showMigration = () => {
            if (loggedIn && !seenMigration) {
                this.props.actions.setPopup(newMigrationPopup(this.props.actions.clearPopup));
            } else {
                this.props.actions.clearPopup();
            }
        };

        if (!agreedToTerms) {
            this.props.actions.setPopup(newTermsPopup(showMigration));
        } else {
            showMigration();
        }
    }

    /**
     * withAccount restricts a component to rendering only if the user is logged
     * in.
     */
    public withAccount = <T extends React.ComponentClass | React.StatelessComponent>(component: T): React.ComponentClass | React.StatelessComponent => {
        const { address } = this.props;
        const { checkingReLogin } = this.state;

        // Show a loading spinner if retrieving the web3 instance is taking a
        // while (for example, when requesting MetaMask access)
        if (checkingReLogin) {
            return () => <LoggingOut />;
        }

        if (!address) {
            const pathname = window.location.pathname;
            if (pathname !== "/" && pathname !== "/loading") {
                this.props.actions.setAlert({
                    alert: new Alert({ message: "You are not logged in. Please select your wallet to continue", alertType: AlertType.Warning })
                });
                this.props.actions.storeURL(pathname);
            }

            return () => <Redirect to="/" />;
        }

        return component;
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        return (
            <Router history={history}>
                <div className="app">
                    {_catch_(<BackgroundTasks />)}
                    <ScrollToTop />
                    <Route path="/" exact component={Home} />
                    <Route path="/exchange" component={this.withAccount(Exchange)} />
                    <Route path="/orders" component={this.withAccount(Orders)} />
                    <Route path="/balances" component={this.withAccount(Balances)} />
                    <Route path="/loading" component={LoggingOut} />
                    {_catch_(<Alerts />)}
                    {_catch_(<Popup />)}
                </div>
            </Router>
        );
    }

    /**
     * handleReLogin checks if a user's previous logged-in session can be
     * recovered. If it can't such as for keystore wallets and hardware wallets,
     * it logs the user out.
     */
    private readonly handleReLogin = async (): Promise<boolean> => {
        const { address, actions, sdk, url } = this.props;
        if (address) {
            let provider;
            try {
                provider = await getInjectedWeb3Provider();
            } catch (error) {
                // Injected Web3 request was denied
                return false;
            }

            const included = await includesAddress(new Web3(provider), address);
            if (included) {
                // These are repeated in login, but the page will log-out if the
                // address is not available immediately
                sdk.updateProvider(provider);
                sdk.setAddress(address);
                await actions.login(sdk, provider, address, { redirect: false, url });

                return true;
            }
        }

        return false;
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    address: state.trader.address,
    agreedToTerms: state.trader.agreedToTerms,
    seenMigration: state.trader.seenMigration,
    sdk: state.sdk.sdk,
    url: state.trader.url,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        clearPopup,
        login,
        logout,
        setAlert,
        setPopup,
        storeURL,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
}

interface State {
    /**
     * When the page is first loaded, the component checks the user
     * can be logged in automatically. While this is happening, we show a
     * spinner based on if checkingReLogin is true or not.
     */
    checkingReLogin: boolean;
}

export const App = connect(mapStateToProps, mapDispatchToProps)(AppClass);
