import BigNumber from "bignumber.js";
import BN from "bn.js";

import { TokenCode } from "@renex/renex";
import { TokenDetails, UnknownToken } from "./market";

/**
 * Convert a token amount to the readable amount using the token decimals.
 * @param balance The balance represented as a BigNumber.
 * @param token The token used to represented the balance.
 */
export const balanceToReadable = (balanceIn: BN, token: TokenCode): BigNumber => {
    const balance = balanceIn === undefined ? new BN(0) : balanceIn;

    // If token is not found, use `UnknownToken` which has 0 decimals
    // i.e. the balance is returned as it is
    const tokenDetails = TokenDetails.get(token, UnknownToken);

    let balanceBN = new BigNumber(balance.toString());

    const e = new BigNumber(10).pow(tokenDetails.digits);
    balanceBN = balanceBN.div(e);

    return balanceBN;
};

/**
 * Convert a readable amount to the token amount using the token decimals.
 * @param readable The amount represented as a string.
 * @param token The token used to represent the amount.
 */
export const readableToBalance = (readableIn: string, token: TokenCode): BN => {
    const readable = readableIn === undefined || readableIn === "" ? "0" : readableIn;

    // If token is not found, use `UnknownToken` which has 0 decimals
    // i.e. the balance is returned as it is
    const tokenDetails = TokenDetails.get(token, UnknownToken);

    const e = new BigNumber(10).pow(tokenDetails.digits);

    return new BN(new BigNumber(readable)
        .times(e)
        .integerValue(BigNumber.ROUND_FLOOR)
        .toFixed());
};

/**
 * Removes excessive digits from a value for a given currency. Primarily used
 * for user inputs.
 * @param amount The amount to be checked for excessive digits.
 * @param token The token the digits should be checked against.
 */
export const removeExcessDigits = (amount: BigNumber, token: TokenCode): BigNumber => {
    const value = readableToBalance(amount.toFixed(), token);
    return new BigNumber(balanceToReadable(value, token));
};
