import * as React from "react";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { Link } from "react-router-dom";
import { bindActionCreators, Dispatch } from "redux";

import { setMarket } from "../actions/inputs/inputsActions";
import { Pair, PairDetails, Pairs, TokenDetails } from "../lib/market";
import { ApplicationData } from "../reducers/types";

/**
 * MarketBanner is a visual component for displaying market price changes in a
 * banner format
 */
class MarketBannerClass extends React.Component<Props, State> {
    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        return (
            <div className="market">
                <div className="container">
                    <ul className="market--items">
                        {this.listPairs()}
                    </ul>
                </div>
            </div>
        );
    }

    private listPairs(): React.ReactNode[] {
        const { marketPrices } = this.props;
        let key = 0;
        return Pairs
            .map((pairDetails: PairDetails) => {
                key += 1;
                const pair = pairDetails.code;
                const marketPrice = marketPrices.get(pair, undefined);
                if (marketPrice === undefined || pair === Pair.BTC_WBTC) {
                    return <div key={key} />;
                }
                const percentChange = marketPrice ? `${marketPrice.percentChange.toFixed(2)}%` : "";
                const price = marketPrice ? marketPrice.price : "";
                const fstDetails = TokenDetails.get(pairDetails.quoteToken, undefined);
                const sndDetails = TokenDetails.get(pairDetails.baseToken, undefined);

                if (!fstDetails || !sndDetails) {
                    return <div key={key} />;
                }

                return (
                    <li key={key} title={price.toString()}>
                        {/* tslint:disable-next-line:react-this-binding-issue */}
                        <Link to="/exchange" onClick={this.updateMarket.bind(this, pair)}>
                            <span>{`${sndDetails.symbol}/${fstDetails.symbol}`}:</span>
                        </Link>
                        <span className={`${marketPrice.percentChange < 0 ? "negative" : "positive"}`}>{percentChange}</span>
                    </li>
                );
            })
            .toArray();
    }

    private updateMarket(market: Pair): void {
        if (market !== this.props.market) {
            this.props.actions.setMarket({ market });
        }
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    market: state.inputs.market,
    marketPrices: state.marketPrices.marketPrices,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        setMarket,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
}

interface State {
}

export const MarketBanner = connect(mapStateToProps, mapDispatchToProps)(MarketBannerClass);
