import * as React from "react";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { BalanceAction, BalanceActionType, Token, TokenCode, TransactionStatus } from "@renex/renex";

import { setAlert } from "../actions/alert/alertActions";
import { withdraw } from "../actions/trader/balanceActions";
import { _captureInteractionException_ } from "../lib/errors";
import { RenExTokens, TokenDetails, UnknownToken } from "../lib/market";
import { networkData } from "../lib/network";
import { ErrorCanceledByUser } from "../lib/wallets/wallet";
import { Alert, AlertType, ApplicationData, LabelLevel } from "../reducers/types";
import { InfoLabel } from "./InfoLabel";
import { Loading } from "./Loading";

/**
 * ViewBalancesTable is a visual component for allowing a user to deposit and
 * withdraw funds
 */
class ViewBalancesTableClass extends React.Component<Props, State> {
    public constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            balanceHistorySize: 0,
            pendingWithdrawal: false,
            withdrawing: null,
        };
    }

    public componentWillReceiveProps(nextProps: Props): void {
        const { balanceHistory } = nextProps;

        // Check for pending withdrawal updates if there are new items in the
        // history or if there is an existing pending withdrawal.
        if (balanceHistory.size !== this.state.balanceHistorySize || this.state.pendingWithdrawal) {
            this.setState({ balanceHistorySize: balanceHistory.size });
            const pendingWithdrawal = balanceHistory.filter((item: BalanceAction | undefined) => {
                return item !== undefined && item.action === BalanceActionType.Withdraw && item.status === TransactionStatus.Pending;
            }).size > 0;
            if (pendingWithdrawal !== this.state.pendingWithdrawal) {
                this.setState({ pendingWithdrawal });
            }
        }
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {

        return <div className="view--table table--wrapper">
            <table>
                <thead>
                    <tr>
                        <th>Token</th>
                        <th>
                            Locked Balance
                                <InfoLabel>If you have any ordered open, please cancel them before withdrawing.</InfoLabel>
                        </th>
                        <th>
                            Usable Balance
                                <InfoLabel>The amount you have deposited, excluding balances locked in open orders or pending withdrawals.</InfoLabel>
                        </th>
                        <th />
                    </tr>
                </thead>
                <tbody>
                    {this.generateRows()}
                </tbody>
            </table>
        </div>;
    }

    private generateRows(): React.ReactNode[] {
        const { legacyBalances, usableLegacyBalances, supportedTokens } = this.props;
        const { withdrawing } = this.state;

        const list: React.ReactNode[] = [];
        RenExTokens(supportedTokens)
            .forEach((token: TokenCode) => {
                const { name, symbol, icon, address } = TokenDetails.get(token, UnknownToken);
                const balance = legacyBalances.get(token, null);
                const usableBalance = usableLegacyBalances.get(token, null);

                if (balance && balance.isZero()) {
                    return;
                }

                const lockedBalance = balance && usableBalance ? balance.minus(usableBalance) : balance;

                const lockedBalanceRender = !lockedBalance || lockedBalance.isZero() ?
                    `` :
                    <span className="red">{lockedBalance.toFixed()} {symbol}</span>;

                const readableUsableBalanceText = (usableBalance !== null) ? usableBalance.toFixed() : "-";

                // tslint:disable-next-line:non-literal-require
                const image = require(`../tokens/${icon}`);

                let link;
                if (token !== Token.ETH) {
                    const TOKEN_URL = `${networkData.etherscan}/token/${address}`;
                    link =
                        <a className="view--external" href={TOKEN_URL} rel="noopener noreferrer" target="_blank" title="View on Etherscan">
                            <span>&rarr;</span>
                        </a>;
                }
                list.push(
                    <tr key={token}>
                        <td>
                            <img alt="" role="presentation" src={image} />
                            <span>{`${name} `}</span>
                            {link}
                        </td>
                        <td>
                            {lockedBalanceRender}
                            {balance === null &&
                                <InfoLabel level={LabelLevel.Warning}>Failed to retrieve locked {symbol} balance.</InfoLabel>
                            }
                        </td>
                        <td>
                            {`${readableUsableBalanceText} ${symbol}`}
                            {usableBalance === null &&
                                <InfoLabel level={LabelLevel.Warning}>Failed to retrieve usable {symbol} balance.</InfoLabel>
                            }
                        </td>
                        <td>
                            {withdrawing === token ?
                                <Loading /> :
                                <button disabled={withdrawing !== null} value={token} onClick={this.withdraw}>Withdraw</button>
                            }
                        </td>
                    </tr >
                );
            });

        return list;
    }

    private readonly withdraw = async (event: React.MouseEvent<HTMLButtonElement>): Promise<void> => {

        const { sdk, legacyBalances, usableLegacyBalances } = this.props;
        const { pendingWithdrawal } = this.state;

        const element = (event.target as HTMLButtonElement);
        const currency = element.value;

        const withdrawAmount = usableLegacyBalances.get(currency);

        if (!withdrawAmount) {
            this.props.actions.setAlert({ alert: new Alert({ message: "Unable to withdraw, please try again." }) });
            return;
        }

        if (pendingWithdrawal) {
            this.props.actions.setAlert({ alert: new Alert({ message: "Please wait until the current withdrawal is complete" }) });
            return;
        }

        this.setState({ withdrawing: currency as Token });

        try {
            // tslint:disable-next-line: await-promise
            await this.props.actions.withdraw(
                sdk, legacyBalances, usableLegacyBalances, withdrawAmount.toFixed(), currency,
            );
        } catch (error) {
            if (error.message !== ErrorCanceledByUser) {
                _captureInteractionException_(error, { description: "Error in ViewBalancesTable.withdraw", shownToUser: "As alert" });

                this.props.actions.setAlert({
                    alert: new Alert({
                        alertType: AlertType.Error,
                        message: error.message || error,
                    })
                });
            }
        }

        this.setState({ withdrawing: null });
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    accountLegacyBalances: state.trader.accountLegacyBalances,
    balanceHistory: state.balanceHistory.balanceHistory,
    legacyBalances: state.trader.legacyBalances,
    traderOrders: state.traderBook.traderOrders,
    usableLegacyBalances: state.trader.usableLegacyBalances,
    sdk: state.sdk.sdk,
    supportedTokens: state.sdk.supportedTokens,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        setAlert,
        withdraw,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
}

interface State {
    balanceHistorySize: number;
    pendingWithdrawal: boolean;
    withdrawing: Token | null;
}

export const ViewBalancesTable = connect(mapStateToProps, mapDispatchToProps)(ViewBalancesTableClass);
