import * as Sentry from "@sentry/browser";

import RenExSDK, { TokenCode } from "@renex/renex";
import Web3 from "web3";

import { OrderedMap } from "immutable";
import { Dispatch } from "redux";
import { createStandardAction } from "typesafe-actions";
import { Provider } from "web3/providers";

import { storeBalanceActions } from "../../actions/balances/balancesActions";
import { setMarket } from "../../actions/inputs/inputsActions";
import { addOrders } from "../../actions/orderbook/traderBookActions";
import {
    updateAccountLegacyBalances,
    updateAtomicAddresses,
    updateAtomicBalances,
    updateLegacyBalances,
    updateUsableLegacyBalances,
} from "../../actions/trader/balanceActions";
import { storeWallet } from "../../actions/trader/walletActions";
import { _captureBackgroundException_, _captureInteractionException_ } from "../../lib/errors";
import { history } from "../../lib/history";
import { Pair } from "../../lib/market";
import { INFURA_URL, networkData } from "../../lib/network";
import { getNetwork } from "../../lib/web3";
import { BalanceActionRecord, traderOrderToRecord } from "../../reducers/typesSdk";

export const storeAddress = createStandardAction("STORE_ADDRESS")<string | null>();

export const storeURL = createStandardAction("STORE_URL")<string | null>();

export const logout = (sdk: RenExSDK, options: { reload: boolean }) => async (dispatch: Dispatch) => {

    // Clear session account in store (and in local storage)
    dispatch(storeAddress(null));

    // Clear Redux stores
    dispatch(setMarket({ market: Pair.ETH_REN }));

    // Clear balances
    dispatch(updateLegacyBalances({ balances: OrderedMap() }));
    dispatch(updateUsableLegacyBalances({ usableBalances: OrderedMap() }));
    dispatch(updateAccountLegacyBalances({ accountBalances: OrderedMap() }));
    dispatch(updateAtomicBalances({ atomicBalances: OrderedMap() }));

    dispatch(updateAtomicAddresses({ atomicAddresses: OrderedMap<TokenCode, string>() }));
    dispatch(storeWallet({ wallet: null }));

    // Use read-only provider and clear address
    sdk.updateProvider(new Web3.providers.HttpProvider(INFURA_URL));
    sdk.setAddress("");

    Sentry.configureScope((scope) => {
        scope.setExtra("loggedIn", false);
    });

    if (options.reload) {
        history.push("/loading");
        // Reload to clear all stores and cancel timeouts
        // (e.g. deposit/withdrawal confirmations)
        location.replace("/");
    }
};

export const login = (
    sdk: RenExSDK,
    web3Provider: Provider,
    address: string,
    options: { redirect: boolean; url: string | null }
) => async (dispatch: Dispatch) => {
    // Check that the provider is using the correct network
    if (await getNetwork(new Web3(web3Provider)) !== networkData.ethNetwork) {
        // dispatch(setAlert({
        //     alert: new Alert({ message: `Invalid Web3 network (expected ${networkData.ethNetworkLabel})` })
        // }));

        logout(sdk, { reload: false })(dispatch)
            .catch((error) => {
                _captureInteractionException_(
                    error,
                    { description: "accountActions.login.logout", shownToUser: "No" });
            });

        return;
    }

    Sentry.configureScope((scope) => {
        scope.setUser({ id: address });
        scope.setExtra("loggedIn", true);
    });

    // Store address in the store (and in local storage)
    dispatch(storeAddress(address));

    // Configure SDK
    sdk.updateProvider(web3Provider);
    sdk.setAddress(address);

    // Retrieve the trader's orders
    sdk.fetchTraderOrders()
        .then((traderOrders) => dispatch(addOrders(traderOrders.map(traderOrderToRecord))))
        .catch((error) => {
            _captureBackgroundException_(
                error,
                { description: "accountActions.login.fetchTraderOrders" });
        });

    // Retrieve the trader's balance actions
    sdk.fetchBalanceActions()
        // Convert to immutable records and then dispatch
        .then((balanceActions) => dispatch(storeBalanceActions(
            balanceActions.map(balanceAction => new BalanceActionRecord(balanceAction))
        )))
        .catch((error) => {
            _captureBackgroundException_(
                error,
                { description: "accountActions.login.fetchBalanceActions" });
        });

    if (options.redirect) {
        history.push(options.url ? options.url : "/exchange");
    }
};

export const lookForLogout = (sdk: RenExSDK) => async (dispatch: Dispatch) => {
    if (!sdk.getAddress()) {
        return;
    }

    let accounts = await sdk.getWeb3().eth
        .getAccounts();

    accounts = accounts.map((account) => account.toLowerCase());

    const address = sdk.getAddress()
        .toLowerCase();

    if (!accounts.includes(address)) {
        console.warn(`User has logged out of their web3 provider (${sdk.getAddress()} not in [${accounts.join(", ")}])`);
        logout(sdk, { reload: true })(dispatch)
            .catch((error) => {
                _captureBackgroundException_(
                    error,
                    { description: "accountActions.lookForLogout.logout" });
            });
    }
};
